        h4 {\r\n            color: #919aa3;\r\n            font-size: 0.875rem;\r\n            margin-top: 5px;\r\n        }\r\n        span {\r\n            font-size: 1rem;\r\n        }\r\n        li {\r\n            display: inline-block;\r\n            margin-right: 60px;\r\n            text-align: left;\r\n            &:last-child {\r\n                margin-right: 0;\r\n                position: absolute;\r\n                right: 0;\r\n                top: 10px;\r\n                font-size: 20px;\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n\r\n/*====== Ecommerce Dashboard Css End ======*/\r\n\r\n.login .container-fluid {\r\n    width: auto;\r\n    margin-top: 80px;\r\n}\r\n\r\n.filter-bar .navbar .navbar-nav .dropdown-menu {\r\n    position: absolute;\r\n}\r\n\r\n\r\n/*====== Ready to use Css Start ======*/\r\n\r\n.wrapper {\r\n    padding: 0;\r\n}\r\n\r\n\r\n/*====== Ready to use Css End ======*/\r\n\r\n.card {\r\n    border-radius: 5px;\r\n    box-shadow: 0 1px 20px 0 rgba(69, 90, 100, 0.08);\r\n    // box-shadow: 0 10px 20px -4px rgba(0,0,0,0.16);\r\n    border: none;\r\n    margin-bottom: 30px;\r\n    // overflow: hidden;\r\n    transition: all 0.3s ease-in-out;\r\n    .card-footer {\r\n        border-top: none;\r\n        padding: 15px 20px;\r\n        border-radius: 0 0 5px 5px;\r\n    }\r\n    .card-header {\r\n        background-color: transparent;\r\n        border-bottom: none;\r\n        padding: 12px;\r\n        position: relative;\r\n        box-shadow: 0px 1px 20px 0px rgba(69, 90, 100, 0.08);\r\n        .card-header-left {\r\n            display: inline-block;\r\n        }\r\n        .card-header-right {\r\n            right: 10px;\r\n            top: 13px;\r\n            display: inline-block;\r\n            float: right;\r\n            padding: 7px 0;\r\n            position: absolute;\r\n            @media only screen and (max-width: 575px) {\r\n                display: none;\r\n            }\r\n            i {\r\n                mar