info-color, 0.5)),\r\n    disabled: (\r\n        base: $disabled-color,\r\n        hover: lighten($disabled-color, 10%),\r\n        active: darken($disabled-color, 12%),\r\n        trans: transparentize($disabled-color, 0.5))\r\n);\r\n@function theme-color($color-name, $color-variant) {\r\n    @return map-get(map-get($theme-color, $color-name), $color-variant);\r\n}\r\n\r\n// Elements color\r\n$border-color:#ddd;\r\n$light-color-text: #fff;","/**  =====================\r\n      Generic-class css start\r\n========================== **/\r\n\r\n.generic-card-block code {\r\n    cursor: pointer;\r\n    display: inline-block;\r\n    margin-right: 10px;\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.generic-image-body {\r\n    li {\r\n        padding: 0 20px;\r\n        display: inline-block;\r\n    }\r\n    code {\r\n        display: block;\r\n    }\r\n    img {\r\n        display: block;\r\n        margin: 10px auto 20px;\r\n    }\r\n}\r\n\r\n\r\n/*====== Padding css starts ======*/\r\n\r\n$i:0;\r\n@while $i<=50 {\r\n    .p-#{$i} {\r\n        padding:#{$i}px;\r\n    }\r\n    $i:$i+5;\r\n}\r\n\r\n\r\n/*====== Padding css ends ======*/\r\n\r\n\r\n/*====== Padding-top css starts ======*/\r\n\r\n$i:0;\r\n@while $i<=50 {\r\n    .p-t-#{$i} {\r\n        padding-top:#{$i}px !important;\r\n    }\r\n    $i:$i+5;\r\n}\r\n\r\n\r\n/*====== Padding-top css ends ======*/\r\n\r\n\r\n/*====== Padding-bottom css starts ======*/\r\n\r\n$i:0;\r\n@while $i<=50 {\r\n    .p-b-#{$i} {\r\n        padding-bottom:#{$i}px !important;\r\n    }\r\n    $i:$i+5;\r\n}\r\n\r\n\r\n/*====== Padding-bottom css ends ======*/\r\n\r\n\r\n/*====== Padding-left css starts ======*/\r\n\r\n$i:0;\r\n@while $i<=50 {\r\n    .p-l-#{$i} {\r\n        padding-left:#{$i}px;\r\n    }\r\n    $i:$i+5;\r\n}\r\n\r\n\r\n/*====== Padding-left css ends ======*/\r\n\r\n\r\n/*====== Padding-right css starts ======*/\r\n\r\n$i:0;\r\n@while $i<=50 {\r\n    .p-r-#{$i} {\r\n        padding-right:#{$i}px;\r\n    }\r\n    $i:$i+5;\r\n}\r\n\r\n\r\n/*====== Padding-right css ends ======*/\r\n\r\n\r\n/*====== Margin css starts ======*/\r\n\r\n$i:0;\r\n@while $i<=50 {\r\n    .m-#{$i} {\r\n        margin:#{$i}px;\r\n    }\r\n    $i:$i+5;\r\n}\r\n\r\n\r\n/*====== Margin css ends ======*/\r\n\r\n\r\n/*====== Margin-top css starts ======*/\r\n\r\n$i:0;\r\n@while $i<=50 {\r\n    .m-t-#{$i} {\r\n        margin-top:#{$i}px;\r\n    }\r\n    $i:$i+5;\r\n}\r\n\r\n\r\n/*====== Margin-top css ends ======*/\r\n\r\n\r\n/*====== Margin-Bottom css starts ======*/\r\n\r\n$i:0;\r\n@while $i<=50 {\r\n    .m-b-#{$i} {\r\n        margin-bottom:#{$i}px;\r\n    }\r\n    $i:$i+5;\r\n}\r\n\r\n\r\n/*====== Margin-Bottom css ends ======*/\r\n\r\n\r\n/*====== Margin-left css starts ======*/\r\n\r\n$i:0;\r\n@while $i<=50 {\r\n    .m-l-#{$i} {\r\n        margin-left:#{$i}px;\r\n    }\r\n    $i:$i+5;\r\n}\r\n\r\n\r\n/*====== Margin-left css ends ======*/\r\n\r\n\r\n/*====== Margin-right css starts ======*/\r\n\r\n$i:0;\r\n@while $i<=50 {\r\n    .m-r-#{$i} {\r\n        margin-right:#{$i}px;\r\n    }\r\n    $i:$i+5;\r\n}\r\n\r\n\r\n/*====== Margin-right css ends ======*/\r\n\r\n\r\n/*====== Display css starts ======*/\r\n\r\n$i:none,\r\ninline-block,\r\ninline-flex;\r\n@each $val in $i {\r\n    .d-#{$val} {\r\n        display: $val;\r\n    }\r\n}\r\n\r\n\r\n/*====== display css ends ======*/\r\n\r\n\r\n/*====== Border-radius css starts ======*/\r\n\r\n$i:0;\r\n@while $i<=10 {\r\n    .b-radius-#{$i} {\r\n        border-radius:#{$i}px !important;\r\n    }\r\n    $i:$i+5;\r\n}\r\n\r\n\r\n/*====== Border-radius css ends ======*/\r\n\r\n\r\n/*====== Font-size css starts ======*/\r\n\r\n$i:10;\r\n@while $i<=80 {\r\n    .f-#{$i} {\r\n        font-size:#{$i}px;\r\n    }\r\n    $i:$i+2;\r\n}\r\n\r\n\r\n/*====== Font-size css ends ======*/\r\n\r\n\r\n/*====== Font-weight css starts ======*/\r\n\r\n$i:100,\r\n300,\r\n400,\r\n600,\r\n700,\r\n900;\r\n@each $val in $i {\r\n    .f-w-#{$val} {\r\n        @if($val==900){\r\n            font-weight: 600;\r\n        }@else{\r\n            font-weight: $val;\r\n        }\r\n    }\r\n}\r\n.f-w-600{\r\n    font-weight: 500;\r\n}\r\n\r\n\r\n/*====== Font-weight css ends ======*/\r\n\r\n\r\n/*====== Font-style css starts ======*/\r\n\r\n$i:normal,\r\nitalic,\r\noblique,\r\ninitial,\r\ninherit;\r\n@each $val in $i {\r\n    .f-s-#{$val} {\r\n        font-style: $val;\r\n    }\r\n}\r\n\r\n\r\n/*====== Font-style css ends ======*/\r\n\r\n\r\n/*====== Text-align css starts ======*/\r\n\r\n$i:center,\r\nleft,\r\nright;\r\n@each $val in $i {\r\n    .text-#{$val} {\r\n        text-align: $val;\r\n    }\r\n}\r\n\r\n\r\n/*====== Text-align css ends ======*/\r\n\r\n\r\n/*====== Text-Transform css starts ======*/\r\n\r\n$i:capitalize,\r\nuppercase,\r\nlowercase;\r\n@each $val in $i {\r\n    .text-#{$val} {\r\n        text-transform: $val;\r\n    }\r\n}\r\n\r\n\r\n/*====== Text-Transform css ends ======*/\r\n\r\n\r\n/*====== Text-Decoration css starts ======*/\r\n\r\n$i:overline,\r\nline-through,\r\nunderline;\r\n@each $val in $i {\r\n    .text-#{$val} {\r\n        text-decoration: $val;\r\n    }\r\n}\r\n\r\n\r\n/*====== Text-Decoration css ends ======*/\r\n\r\n\r\n/*====== Vertical-Align css starts ======*/\r\n\r\n$i:baseline,\r\nsub,\r\nsuper,\r\ntop,\r\ntext-top,\r\nmiddle,\r\nbottom,\r\ntext-bottom,\r\ninitial,\r\ninherit;\r\n@each $val in $i {\r\n    .#{$val} {\r\n        vertical-align: $val;\r\n    }\r\n}\r\n\r\n\r\n/*====== Vertical-Align css ends ======*/\r\n\r\n\r\n/*====== Position css starts ======*/\r\n\r\n$i:static,\r\nabsolute,\r\nfixed,\r\nrelative,\r\ninitial,\r\ninherit;\r\n@each $val in $i {\r\n    .pos-#{$val} {\r\n        position: $val;\r\n    }\r\n}\r\n\r\n\r\n/*====== Position css ends ======*/\r\n\r\n\r\n/*====== Float css starts ======*/\r\n\r\n$i:left,\r\nright,\r\nnone;\r\n@each $val in $i {\r\n    .f-#{$val} {\r\n        float: $val;\r\n    }\r\n}\r\n\r\n\r\n/*====== Float css ends ======*/\r\n\r\n\r\n/*====== Overflow css starts ======*/\r\n\r\n$i:hidden,\r\nvisible,\r\nauto;\r\n@each $val in $i {\r\n    .o-#{$val} {\r\n        overflow: $val;\r\n    }\r\n}\r\n\r\n\r\n/*====== Overflow css ends ======*/\r\n\r\n\r\n/*====== Image-sizes css starts ======*/\r\n\r\n$i:20;\r\n@while $i<=100 {\r\n    .img-#{$i} {\r\n        width:#{$i}px;\r\n    }\r\n    $i:$i+10;\r\n}\r\n\r\n\r\n/*====== Image-sizes css ends ======*/\r\n\r\n\r\n/*======= Text css starts ===========*/\r\n\r\n$custom-color:#64b0f2;\r\n$pink-color:#ff7aa3;\r\n$muted-color:#78909c;\r\n$dark-color:#2b3d51;\r\n$purple-color:#9261c6;\r\n$text-name:text-primary,\r\ntext-warning,\r\ntext-default,\r\ntext-danger,\r\ntext-success,\r\ntext-inverse,\r\ntext-info,\r\ntext-custom,\r\ntext-pink,\r\ntext-dark,\r\ntext-purple,\r\ntext-muted;\r\n$text-color:$primary-color,\r\n$warning-color,\r\n$default-color,\r\n$danger-color,\r\n$success-color,\r\n$inverse-color,\r\n$info-color,\r\n$custom-color,\r\n$pink-color,\r\n$dark-color,\r\n$purple-color,\r\n$muted-color;\r\n@each $var in $text-name {\r\n    $i: index($text-name, $var);\r\n    .#{$var} {\r\n        color: nth($text-color, $i) !important;\r\n    }\r\n}\r\n\r\n\r\n/*======= Text css ends ===========*/\r\n\r\n\r\n/*======= Label-color css starts  ======= */\r\n\r\n.label {\r\n    border-radius: 2px;\r\n    color: #fff;\r\n    font-size: 12px;\r\n    line-height: 1;\r\n    margin-bottom: 0;\r\n    text-transform: capitalize;\r\n}\r\n\r\n$label-name:primary,\r\nwarning,\r\ndefault,\r\ndanger,\r\nsuccess,\r\ninverse,\r\ninfo;\r\n$label-color:$primary-color,\r\n$warning-color,\r\n$default-color,\r\n$danger-color,\r\n$success-color,\r\n$inverse-color,\r\n$info-color;\r\n@each $var in $label-name {\r\n    $i: index($label-name, $var);\r\n    .label-#{$var} {\r\n        background:nth($label-color,$i);\r\n    }\r\n}\r\n.label-warning{\r\n    color:#fff;\r\n}\r\n\r\n/*======= Label-color css starts  ======= */\r\n\r\n\r\n/*======= Badge-color css starts  ======= */\r\n\r\n$badge-name:primary,\r\nwarning,\r\ndefault,\r\ndanger,\r\nsuccess,\r\ninverse,\r\ninfo;\r\n$badge-color:$primary-color,\r\n$warning-color,\r\n$default-color,\r\n$danger-color,\r\n$success-color,\r\n$inverse-color,\r\n$info-color;\r\n@each $var in $badge-name {\r\n    $i: index($badge-name, $var);\r\n    .badge-#{$var} {\r\n        background:nth($badge-color,$i);\r\n    }\r\n}\r\n\r\n\r\n/*======= Badge-color css end  ======= */\r\n\r\n\r\n/*======= Background-color css starts  ======= */\r\n\r\n.bg-color-box {\r\n    text-align: center;\r\n    display: inline-block;\r\n    padding: 10px 20px;\r\n    box-shadow: 0 0 5px 0 rgba(128, 128, 128, 0.49);\r\n    font-weight: 600;\r\n    margin-right: 20px;\r\n    margin-bottom: 20px;\r\n    cursor: pointer;\r\n}\r\n\r\n.bg-color-box span {\r\n    color: #fff;\r\n}\r\n\r\n$background-name:primary,\r\nwarning,\r\ndefault,\r\ndanger,\r\nsuccess,\r\ninverse,\r\ninfo;\r\n$background-color:$primary-color,\r\n$warning-color,\r\n$default-color,\r\n$danger-color,\r\n$success-color,\r\n$inverse-color,\r\n$info-color;\r\n@each $var in $background-name {\r\n    $i: index($background-name, $var);\r\n    .bg-#{$var} {\r\n        background-color: nth($label-color, $i) !important;\r\n        color: #fff;\r\n    }\r\n}\r\n\r\n\r\n/*======= Background-color css end  ======= */\r\n\r\n\r\n/*======= Button-color css starts  ======= */\r\n\r\n$btn-name:primary,\r\nwarning,\r\ndefault,\r\ndanger,\r\nsuccess,\r\ninverse,\r\ninfo;\r\n$btn-color:$primary-color,\r\n$warning-color,\r\n$default-color,\r\n$danger-color,\r\n$success-color,\r\n$inverse-color,\r\n$info-color;\r\n@each $var in $btn-name {\r\n    $i: index($btn-name, $var);\r\n    .color-#{$var} {\r\n        background-color: nth($btn-color, $i);\r\n    }\r\n}\r\n\r\n\r\n/*======= Button-color css ends  ======= */\r\n\r\n\r\n/*======= Loader-color css starts  ======= */\r\n\r\n$loader-name:primary,\r\nwarning,\r\ndefault,\r\ndanger,\r\nsuccess,\r\ninverse,\r\ninfo;\r\n$loader-color:$primary-color,\r\n$warning-color,\r\n$default-color,\r\n$danger-color,\r\n$success-color,\r\n$inverse-color,\r\n$info-color;\r\n@each $var in $loader-name {\r\n    $i: index($loader-name, $var);\r\n    .loader-#{$var} {\r\n        background-color: nth($loader-color, $i) !important;\r\n    }\r\n}\r\n\r\n\r\n/*======= Loader-color css ends  ======= */\r\n\r\n\r\n/*======= Nestable-color css starts  ======= */\r\n\r\n$nestable-name:primary,\r\nwarning,\r\ndefault,\r\ndanger,\r\nsuccess,\r\ninverse,\r\ninfo;\r\n$nestable-color:$primary-color,\r\n$warning-color,\r\n$default-color,\r\n$danger-color,\r\n$success-color,\r\n$inverse-color,\r\n$info-color;\r\n@each $var in $nestable-name {\r\n    $i: index($nestable-name, $var);\r\n    .nestable-#{$var} {\r\n        background-color: nth($nestable-color, $i) !important;\r\n        border-color: nth($nestable-color, $i);\r\n        color: #fff !important;\r\n    }\r\n}\r\n\r\n\r\n/*======= Nestable-color css ends  ======= */\r\n\r\n\r\n/*======= Table-Border-Bottom-color css starts  ======= */\r\n\r\n$border-name:primary,\r\nwarning,\r\ndefault,\r\ndanger,\r\nsuccess,\r\ninverse,\r\ninfo;\r\n$border-color:$primary-color,\r\n$warning-color,\r\n$default-color,\r\n$danger-color,\r\n$success-color,\r\n$inverse-color,\r\n$info-color;\r\n@each $var in $border-name {\r\n    $i: index($border-name, $var);\r\n    table thead .border-bottom-#{$var} th,\r\n    table tbody .border-bottom-#{$var} th,\r\n    table tbody .border-bottom-#{$var} td {\r\n        border-bottom: 1px solid nth($border-color, $i);\r\n    }\r\n}\r\n\r\n\r\n/*======= Table-Border-Bottom-color css ends  ======= */\r\n\r\n\r\n/*======= Table styling css starts  ======= */\r\n\r\n$table-name:primary,\r\nwarning,\r\ndefault,\r\ndanger,\r\nsuccess,\r\ninverse,\r\ninfo;\r\n$table-color:$primary-color,\r\n$warning-color,\r\n$default-color,\r\n$danger-color,\r\n$success-color,\r\n$inverse-color,\r\n$info-color;\r\n@each $var in $table-name {\r\n    $i: index($table-name, $var);\r\n    .table-styling .table-#{$var},\r\n    .table-styling.table-#{$var} {\r\n        background-color: nth($border-color, $i);\r\n        color: #fff;\r\n        border: 3px solid nth($border-color, $i);\r\n        thead {\r\n            background-color: darken(nth($border-color, $i), 10%);\r\n            border: 3px solid darken(nth($border-color, $i), 10%);\r\n        }\r\n    }\r\n}\r\n\r\n\r\n/*======= Table styling css ends  ======= */\r\n\r\n\r\n/*======= Toolbar css starts  ======= */\r\n\r\n$light:#bdc3c7;\r\n$dark:#34495e;\r\n$toolbar-name:primary,\r\nwarning,\r\nlight,\r\ndanger,\r\nsuccess,\r\ndark,\r\ninfo;\r\n$toolbar-color:$primary-color,\r\n$warning-color,\r\n$default-color,\r\n$danger-color,\r\n$success-color,\r\n$inverse-color,\r\n$info-color;\r\n@each $var in $toolbar-name {\r\n    $i: index($toolbar-name, $var);\r\n    .toolbar-#{$var} .tool-item {\r\n        background: nth($toolbar-color, $i) !important;\r\n    }\r\n    .toolbar-#{$var} .tool-item.selected,\r\n    .toolbar-#{$var} .tool-item:hover {\r\n        background: darken(nth($toolbar-color, $i), 10%) !important;\r\n    }\r\n    .toolbar-#{$var}.tool-top .arrow {\r\n        border-color: nth($toolbar-color, $i) transparent transparent;\r\n    }\r\n    .toolbar-#{$var}.tool-bottom .arrow {\r\n        border-color: transparent transparent nth($toolbar-color, $i);\r\n    }\r\n    .toolbar-#{$var}.tool-left .arrow {\r\n        border-color: transparent transparent transparent nth($toolbar-color, $i);\r\n    }\r\n    .toolbar-#{$var}.tool-right .arrow {\r\n        border-color: transparent nth($toolbar-color, $i) transparent transparent;\r\n    }\r\n    .btn-toolbar-#{$var}.pressed {\r\n        background-color: nth($toolbar-color, $i);\r\n    }\r\n}\r\n\r\n\r\n/*======= Toolbar css ends  ======= */\r\n\r\n\r\n/*======= Card-Border-Top-color css starts  ======= */\r\n\r\n$card-name:primary,\r\nwarning,\r\ndefault,\r\ndanger,\r\nsuccess,\r\ninverse,\r\ninfo;\r\n$card-color:$primary-color,\r\n$warning-color,\r\n$default-color,\r\n$danger-color,\r\n$success-color,\r\n$inverse-color,\r\n$info-color;\r\n@each $var in $card-name {\r\n    $i: index($card-name, $var);\r\n    .card-border-#{$var} {\r\n        border-top: 4px solid nth($border-color, $i);\r\n    }\r\n}\r\n\r\n\r\n/*======= Card-Border-Top-color css ends  ======= */\r\n\r\n\r\n/*======= Panels-wells-color css starts  ======= */\r\n\r\n$panel-name:primary,\r\nwarning,\r\ndefault,\r\ndanger,\r\nsuccess,\r\ninverse,\r\ninfo;\r\n$panel-color:$primary-color,\r\n$warning-color,\r\n$default-color,\r\n$danger-color,\r\n$success-color,\r\n$inverse-color,\r\n$info-color;\r\n@each $var in $panel-name {\r\n    $i: index($panel-name, $var);\r\n    .panels-wells .panel-#{$var} {\r\n        border-color: nth($border-color, $i);\r\n    }\r\n}\r\n\r\n\r\n/*======= Panels-wells-color css ends  ======= */\r\n\r\n\r\n/*======= All-Borders-color css starts  ======= */\r\n\r\n$b-name:primary,\r\nwarning,\r\ndefault,\r\ndanger,\r\nsuccess,\r\ninverse,\r\ninfo,\r\ntheme;\r\n$b-color:$primary-color,\r\n$warning-color,\r\n$default-color,\r\n$danger-color,\r\n$success-color,\r\n$inverse-color,\r\n$info-color,\r\n$theme-border;\r\n@each $var in $b-name {\r\n    $i: index($b-name, $var);\r\n    .b-t-#{$var} {\r\n        border-top: 1px solid nth($b-color, $i);\r\n    }\r\n    .b-b-#{$var} {\r\n        border-bottom: 1px solid nth($b-color, $i);\r\n    }\r\n    .b-l-#{$var} {\r\n        border-left: 1px solid nth($b-color, $i);\r\n    }\r\n    .b-r-#{$var} {\r\n        border-right: 1px solid nth($b-color, $i);\r\n    }\r\n    .b-#{$var} {\r\n        border: 1px solid nth($b-color, $i);\r\n    }\r\n}\r\n\r\n\r\n/*======= All-Borders-color css ends  ======= */\r\n/*======= All-color css start  ======= */\r\n@each $value in $social-name {\r\n    $i: index($social-name, $value);\r\n\r\n    .bg-#{$value} {\r\n        background:nth($social-color,$i);\r\n    }\r\n\r\n    .text-#{$value} {\r\n        color: nth($social-color,$i);\r\n    }\r\n}\r\n@each $value in $color-name {\r\n    $i: index($color-name, $value);\r\n\r\n    .bg-#{$value} {\r\n        background:nth($color-color,$i),;\r\n    }\r\n\r\n    .text-#{$value} {\r\n        color: nth($color-color,$i);\r\n    }\r\n}\r\n/*======= All-color css ends  ======= */\r\n\r\n\r\n/**====== Generic-class css end ======**/\r\n\r\n\r\n\r\n","$menu-title: #448aff,#b71c1c,#4CAF50,#d84315,#4527a0,#37474f,#000,#999,#fff;\r\n$header-theme: #448aff,#b71c1c,#4CAF50,#d84315,#4527a0,#37474f;\r\n$box-bg-color: #448aff,#b71c1c,#4CAF50,#d84315,#4527a0,#37474f;\r\n$left-navbar-theme: #37474f;\r\n$left-navbar-light-theme: #fff;\r\n$active-item: #448aff,#b71c1c,#4CAF50,#d84315,#4527a0,#37474f,#11c788,#FF5252,#ff784f,#38a9f9,#038f9e,#39ADB5;\r\n$icon-color: #448aff,#b71c1c,#4CAF50,#d84315,#4527a0,#37474f,#7C4DFF,#FF5370;\r\n\r\n.pcoded {\r\n    &[nav-type=\"st1\"] {\r\n        .pcoded-item.pcoded-left-item {\r\n            $i: 1;\r\n            @for $k from 1 through 3 {\r\n                @each $value in $icon-color {\r\n                    > li {\r\n                        &:nth-child(#{$i}) {\r\n                            > a > .pcoded-micon {\r\n                                color: $value;\r\n                            }\r\n                        }\r\n                    }\r\n                    $i: $i+1;\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    &[nav-type=\"st2\"] {\r\n        .pcoded-item.pcoded-left-item {\r\n            > li {\r\n                > a > .pcoded-micon {\r\n                    color: $inverse-color;\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    &[nav-type=\"st1\"],\r\n    &[nav-type=\"st2\"] {\r\n        .pcoded-item.pcoded-left-item {\r\n            > li {\r\n                > a > .pcoded-micon {\r\n                    color: $inverse-color;\r\n\r\n                    b {\r\n                        display: none;\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n// icon color end\r\n/**  =====================\r\n      Left-side-menu css start\r\n==========================  **/\r\n.main-menu {\r\n    float: left;\r\n    width: 235px;\r\n    z-index: 99;\r\n    height: 100% !important;\r\n    @media only screen and (max-width: 992px) {\r\n        height: 100% !important;\r\n    }\r\n\r\n    .main-menu-header {\r\n        padding: 15px 0;\r\n        // background-image: url(\"./../../../assets/images/user-bg.jpg\");\r\n        background-size: cover;\r\n        text-align: center;\r\n        position: relative;\r\n        img{\r\n            position: relative;\r\n            width: 60px;\r\n        }\r\n        .user-details {\r\n            display: block;\r\n            margin-top: 10px;\r\n            position: relative;\r\n\r\n            #more-details {\r\n                cursor: pointer;\r\n            }\r\n\r\n            span {\r\n                color: #fff;\r\n                display: block;\r\n                font-size: 14px;\r\n                text-transform: capitalize;\r\n\r\n                i {\r\n                    font-size: 10px;\r\n                    margin-left: 10px;\r\n                }\r\n            }\r\n        }\r\n        &:before{\r\n            content: \"\";\r\n            background: rgba(0,0,0,0.2);\r\n            width: 100%;\r\n            height: 100%;\r\n            position: absolute;\r\n            top: 0;\r\n            left: 0;\r\n            z-index: 0;\r\n        }\r\n    }\r\n\r\n    .main-menu-content {\r\n        .more-details {\r\n            display: none;\r\n\r\n            a {\r\n                padding: 10px 25px;\r\n                display: block;\r\n                color: #000;\r\n                transition: all ease-in 0.3s;\r\n\r\n                &:hover {\r\n                    color: $primary-color;\r\n                }\r\n\r\n                i {\r\n                    margin-right: 10px;\r\n                }\r\n            }\r\n        }\r\n\r\n        .menu-search {\r\n            padding: 20px 20px 10px;\r\n        }\r\n\r\n        li {\r\n            position: relative;\r\n        }\r\n    }\r\n}\r\n\r\n.pcoded[theme-layout=\"vertical\"][vertical-nav-type=\"collapsed\"] {\r\n    .main-menu-content,\r\n    .main-menu-header {\r\n        display: none;\r\n    }\r\n\r\n    .main-menu {\r\n        width: 70px;\r\n    }\r\n}\r\n\r\n.sidebar_toggle {\r\n    a {\r\n        margin: -5px;\r\n        line-height: 1;\r\n    }\r\n}\r\n/**  =====================\r\n      Main-header css start\r\n==========================  **/\r\n\r\n.header-navbar {\r\n    min-height: 56px;\r\n    padding: 0;\r\n\r\n    .navbar-wrapper {\r\n        .navbar-logo {\r\n            position: relative;\r\n            align-items: center;\r\n            display: flex;\r\n            float: left;\r\n            height: 56px;\r\n            text-align: center;\r\n            text-transform: uppercase;\r\n            width: 235px;\r\n            padding: 10px;\r\n            @media only screen and (max-width: 992px) {\r\n                justify-content: center;\r\n            }\r\n\r\n            h5 {\r\n                margin-bottom: 0;\r\n            }\r\n\r\n            .mobile-menu {\r\n                position: absolute;\r\n                right: 0;\r\n                font-size: 16px;\r\n                line-height: 3.5;\r\n                width: 40px;\r\n            }\r\n            @media only screen and(max-width: 768px) {\r\n                .mobile-search {\r\n                    display: none !important;\r\n                }\r\n            }\r\n\r\n            .mobile-options,\r\n            .mobile-search {\r\n                display: none;\r\n            }\r\n        }\r\n\r\n        .navbar-container {\r\n            .nav-left {\r\n                float: left;\r\n                margin-bottom: 0;\r\n\r\n                li {\r\n                    float: left;\r\n                    line-height: 3.5;\r\n                    // padding: 0 10px; removing space for breadcrumb by shubhangi\r\n                    padding: 0 2px;\r\n                    position: relative;\r\n                    @media only screen and (max-width: 575px) {\r\n                        padding: 0 5px;\r\n                    }\r\n\r\n                    .sidebar_toggle a {\r\n                        display: none;\r\n                    }\r\n\r\n                    > a {\r\n                        font-size: 16px;\r\n                        display: block;\r\n\r\n                        > i {\r\n                            &.flag-icon {\r\n                                border-radius: 50%;\r\n                                width: 20px;\r\n                                height: 20px;\r\n                            }\r\n                        }\r\n                    }\r\n                }\r\n\r\n                a {\r\n                    //space change by shubhangi\r\n                    padding: 0 0.2rem;\r\n                }\r\n\r\n                .mega-menu-top {\r\n                    i {\r\n                        font-size: 10px;\r\n                        margin-left: 10px;\r\n                    }\r\n                }\r\n            }\r\n\r\n            .nav-right {\r\n                float: right;\r\n                @extend .nav-left;\r\n\r\n                .user-profile {\r\n                    img {\r\n                        margin-right: 10px;\r\n                        width: 40px;\r\n                    }\r\n\r\n                    i {\r\n                        font-size: 10px;\r\n                        margin-left: 10px;\r\n                    }\r\n                }\r\n\r\n                > .header-notification:nth-child(2) {\r\n                    .show-notification {\r\n                        li:first-child:hover {\r\n                            background-color: #fff;\r\n                        }\r\n                    }\r\n                }\r\n            }\r\n\r\n            .header-notification {\r\n                perspective: 1000px;\r\n                z-index: 99;\r\n                position: relative;\r\n\r\n                .show-notification {\r\n                    background: #fff;\r\n                    box-shadow: 0 0 35px 0 rgba(0, 0, 0, 0.25);\r\n                    border-radius: 5px;\r\n                    opacity: 1;\r\n                    position: absolute;\r\n                    right: 0;\r\n                    visibility: visible;\r\n                    width: 24rem;\r\n                    display: none;\r\n                    transform: translateY(20px);\r\n\r\n                    a {\r\n                        color: $theme-font-color;\r\n                    }\r\n\r\n                    li {\r\n                        border-top: 1px solid $border-color;\r\n                        line-height: initial;\r\n                        padding: 1em 20px;\r\n                        width: 100%;\r\n                        transition: all 0.3s ease-in-out;\r\n\r\n                        &:first-child {\r\n                            padding: 10px 20px;\r\n\r\n                            h6 {\r\n                                display: inline-block;\r\n                                font-size: 14px;\r\n                                font-weight: 500;\r\n                                margin-bottom: 0;\r\n                            }\r\n\r\n                            label {\r\n                                float: right;\r\n                            }\r\n                        }\r\n\r\n                        img {\r\n                            width: 40px;\r\n                            margin-right: 10px;\r\n                            align-self: flex-start !important;\r\n                        }\r\n\r\n                        &:hover {\r\n                            background-color: #f1f1f1;\r\n                        }\r\n                    }\r\n\r\n                    &.row li {\r\n                        &:hover {\r\n                            background-color: transparent;\r\n                        }\r\n                    }\r\n\r\n                    .notification-user {\r\n                        font-size: 15px;\r\n                        font-weight: 500;\r\n                        margin-bottom: 5px;\r\n                    }\r\n\r\n                    .notification-msg {\r\n                        margin-bottom: 5px;\r\n                    }\r\n\r\n                    .notification-time {\r\n                        font-size: 12px;\r\n                        color: #919aa3;\r\n                    }\r\n                }\r\n\r\n                .profile-notification {\r\n                    @extend .show-notification;\r\n                    width: 17em;\r\n\r\n                    a {\r\n                        font-size: 15px;\r\n                        padding-left: 0;\r\n                    }\r\n\r\n                    i {\r\n                        font-size: 15px;\r\n                        margin-left: 0;\r\n                        margin-right: 5px;\r\n                    }\r\n\r\n                    li {\r\n                        border-top: none;\r\n                        padding: 0.7em 20px;\r\n\r\n                        &:first-child {\r\n                            padding: 0.7em 20px;\r\n                        }\r\n                    }\r\n                }\r\n\r\n                &.active {\r\n                    &:before {\r\n                        content: \"\";\r\n                        border: 10px solid transparent;\r\n                        border-left-color: #fff;\r\n                        border-top-color: #fff;\r\n                        position: absolute;\r\n                        border-radius: 5px;\r\n                        bottom: -32px;\r\n                        right: 20px;\r\n                        box-shadow: -11px -11px 35px 0 rgba(0, 0, 0, 0.25);\r\n                        transform: rotate(45deg);\r\n                        z-index: 9;\r\n                    }\r\n                }\r\n            }\r\n\r\n            .badge {\r\n                border-radius: 100px;\r\n                right: 10px;\r\n                position: absolute;\r\n                top: 14px;\r\n                padding: 4px;\r\n\r\n                &:after,\r\n                &:before {\r\n                    content: \"\";\r\n                    width: 100%;\r\n                    height: 100%;\r\n                    border-radius: 50%;\r\n                    position: absolute;\r\n                    top: 0;\r\n                    left: 0;\r\n                    box-shadow: inset 0 0 20px 5px rgba(255, 255, 255, 0.4);\r\n                }\r\n\r\n                &:after {\r\n                    animation: not-blink 2.1s cubic-bezier(0.65, 0.815, 0.735, 0.395) infinite;\r\n                }\r\n\r\n                &:before {\r\n                    animation: not-blink 2.1s cubic-bezier(0.165, 0.84, 0.44, 1) infinite;\r\n                    animation-delay: 1.15s;\r\n                }\r\n            }\r\n        }\r\n\r\n        .header-search {\r\n            line-height: 0.8;\r\n\r\n            .main-search {\r\n                padding: 8px 0;\r\n                display: block;\r\n\r\n                .input-group {\r\n                    margin-bottom: 0;\r\n                    background-color: transparent;\r\n                    border-radius: 20px;\r\n\r\n                    .form-control {\r\n                        padding: 0.3rem 0.75rem;\r\n                        // display: none;\r\n                        width: 0;\r\n                        border: none;\r\n                        background-color: transparent;\r\n                    }\r\n\r\n                    .input-group-addon {\r\n                        padding: 0.3rem 0.75rem 0.3rem 0;\r\n                        background-color: transparent;\r\n                        border: none;\r\n\r\n                        &.search-close {\r\n                            padding: 0.3rem 0 0.3rem 0.75rem;\r\n            