ons(theme-color(inverse, base), theme-color(inverse, base));\r\n\r\n    &:hover {\r\n        @include buttons-hover(theme-color(inverse, hover), theme-color(inverse, hover));\r\n    }\r\n\r\n    &:active {\r\n        @include buttons-active(theme-color(inverse, active), theme-color(inverse, active));\r\n    }\r\n\r\n    &:focus {\r\n        @include buttons-focus(theme-color(inverse, hover));\r\n    }\r\n\r\n    &.disabled {\r\n        @include button-trans(theme-color(inverse, trans), theme-color(inverse, trans));\r\n    }\r\n}\r\n\r\n.btn-info {\r\n    @include buttons(theme-color(info, base), theme-color(info, base));\r\n\r\n    &:hover {\r\n        @include buttons-hover(theme-color(info, hover), theme-color(info, hover));\r\n    }\r\n\r\n    &:active {\r\n        @include buttons-active(theme-color(info, active), theme-color(info, active));\r\n    }\r\n\r\n    &:focus {\r\n        @include buttons-focus(theme-color(info, hover));\r\n    }\r\n\r\n    &.disabled {\r\n        @include button-trans(theme-color(info, trans), theme-color(info, trans));\r\n    }\r\n}\r\n\r\n.btn-disabled {\r\n    @include buttons(theme-color(disabled, trans), theme-color(disabled, trans));\r\n}\r\n/**====== Basic-Buttons css end ======**/\r\n/**====== Outline-Buttons css Start ======**/\r\n.btn-outline-primary {\r\n    @include buttons-borders(theme-color(primary, active));\r\n    background-color: transparent;\r\n}\r\n\r\n.btn-outline-warning {\r\n    @include buttons-borders(theme-color(warning, active));\r\n    background-color: transparent;\r\n}\r\n.btn-outline-danger {\r\n    @include buttons-borders(theme-color(danger, active));\r\n    background-color: transparent;\r\n}\r\n\r\n.btn-outline-success {\r\n    @include buttons-borders(theme-color(success, active));\r\n    background-color: transparent;\r\n}\r\n\r\n.btn-outline-inverse {\r\n    @include buttons-borders(theme-color(inverse, active));\r\n    background-color: transparent;\r\n\r\n    &:hover {\r\n        color: #fff;\r\n    }\r\n}\r\n\r\n.btn-outline-warning {\r\n    @include buttons-borders(theme-color(warning, active));\r\n    background-color: transparent;\r\n}\r\n\r\n.btn-outline-info {\r\n    @include buttons-borders(theme-color(info, active));\r\n    background-color: transparent;\r\n}\r\n\r\n.btn-outline-disabled {\r\n    @include buttons-borders(theme-color(disabled, active));\r\n}\r\n/**====== Outline-Buttons css end ======**/\r\n/**====== gradient css Start ======**/\r\n.btn-grd-danger,\r\n.btn-grd-disabled,\r\n.btn-grd-info,\r\n.btn-grd-inverse,\r\n.btn-grd-primary,\r\n.btn-grd-success,\r\n.btn-grd-warning,\r\n.btn-grd-warning {\r\n    background-size: 200% auto;\r\n    transition: 0.5s ease-in-out;\r\n    color: #fff;\r\n\r\n    &:hover {\r\n        background-position: right center;\r\n    }\r\n\r\n    &.hor-grd {\r\n        background-size: auto 200%;\r\n\r\n        &:hover {\r\n            background-position: bottom center;\r\n        }\r\n    }\r\n}\r\n\r\n.btn-grd-primary {\r\n    background-image: linear-gradient(to right, theme-color(primary, hover) 0%, theme-color(primary, active) 51%, theme-color(primary, hover) 100%);\r\n\r\n    &.hor-grd {\r\n        background-image: linear-gradient(to top, theme-color(primary, hover) 0%, theme-color(primary, active) 51%, theme-color(primary, hover) 100%);\r\n    }\r\n}\r\n\r\n.btn-grd-warning {\r\n    background-image: linear-gradient(to right, theme-color(warning, hover) 0%, theme-color(warning, active) 51%, theme-color(warning, hover) 100%);\r\n\r\n    &.hor-grd {\r\n        background-image: linear-gradient(to top, theme-color(warning, hover) 0%, theme-color(warning, active) 51%, theme-color(warning, hover) 100%);\r\n    }\r\n}\r\n\r\n.btn-grd-danger {\r\n    background-image: linear-gradient(to right, theme-color(danger, hover) 0%, theme-color(danger, active) 51%, theme-color(danger, hover) 100%);\r\n\r\n    &.hor-grd {\r\n        background-image: linear-gradient(to top, theme-color(danger, hover) 0%, theme-color(danger, active) 51%, theme-color(danger, hover) 100%);\r\n    }\r\n}\r\n\r\n.btn-grd-success {\r\n    background-image: linear-gradient(to right, theme-color(success, hover) 0%, theme-color(success, active) 51%, theme-color(success, hover) 100%);\r\n\r\n    &.hor-grd {\r\n        background-image: linear-gradient(to top, theme-color(success, hover) 0%, theme-color(success, active) 51%, theme-color(success, hover) 100%);\r\n    }\r\n}\r\n\r\n.btn-grd-inverse {\r\n    background-image: linear-gradient(to right, theme-color(inverse, hover) 0%, theme-color(inverse, active) 51%, theme-color(inverse, hover) 100%);\r\n\r\n    &.hor-grd {\r\n        background-image: linear-gradient(to top, theme-color(inverse, hover) 0%, theme-color(inverse, active) 51%, theme-color(inverse, hover) 100%);\r\n    }\r\n}\r\n\r\n.btn-grd-info {\r\n    background-image: linear-gradient(to right, theme-color(info, hover) 0%, theme-color(info, active) 51%, theme-color(info, hover) 100%);\r\n\r\n    &.hor-grd {\r\n        background-image: linear-gradient(to top, theme-color(info, hover) 0%, theme-color(info, active) 51%, theme-color(info, hover) 100%);\r\n    }\r\n}\r\n\r\n.btn-grd-disabled {\r\n    background-image: linear-gradient(to right, theme-color(disabled, hover) 0%, theme-color(disabled, active) 51%, theme-color(disabled, hover) 100%);\r\n\r\n    &.hor-grd {\r\n        background-image: linear-gradient(to top, theme-color(disabled, hover) 0%, theme-color(disabled, active) 51%, theme-color(disabled, hover) 100%);\r\n    }\r\n}\r\n/**====== gradient-Buttons css end ======**/\r\n\r\n/**====== matrialized-Buttons css Start ======**/\r\n.btn.btn-mat{\r\n    position: relative;\r\n    border-radius: 0;\r\n    border: none;\r\n    &:before{\r\n        content: \"\";\r\n        position: absolute;\r\n        bottom: 0;\r\n        // width: 0;\r\n        left:50%;\r\n        right: 50%;\r\n        height: 3px;\r\n        transition: all 0.3s ease-in-out;\r\n    }\r\n    &:hover{\r\n        border: none;\r\n        &:before{\r\n            // width: 100%;\r\n            left:0;\r\n            right:0;\r\n        }\r\n    }\r\n}\r\n.btn-primary.btn-mat {\r\n    background-color: theme-color(primary, base);\r\n    &:before{\r\n        background-color: theme-color(primary, active);\r\n    }\r\n}\r\n\r\n.btn-warning.btn-mat {\r\n    background-color: theme-color(warning, base);\r\n    &:before{\r\n        background-color: theme-color(warning, active);\r\n    }\r\n}\r\n\r\n.btn-danger.btn-mat {\r\n    background-color: theme-color(danger, base);\r\n    &:before{\r\n        background-color: theme-color(danger, active);\r\n    }\r\n}\r\n\r\n.btn-success.btn-mat {\r\n    background-color: theme-color(success, base);\r\n    &:before{\r\n        background-color: theme-color(success, active);\r\n    }\r\n}\r\n\r\n.btn-inverse.btn-mat {\r\n    background-color: theme-color(inverse, base);\r\n    &:before{\r\n        background-color: theme-color(inverse, active);\r\n    }\r\n}\r\n\r\n.btn-info.btn-mat {\r\n    background-color: theme-color(info, base);\r\n    &:before{\r\n        background-color: theme-color(info, active);\r\n    }\r\n}\r\n\r\n.btn-outline-disabled {\r\n    background-color: theme-color(disabled, base);\r\n    &:before{\r\n        background-color: theme-color(disabled, active);\r\n    }\r\n}\r\n/**====== matrialized css end ======**/\r\n\r\n/**====== Button-Size css start ======**/\r\n.btn-xlg {\r\n    padding: 14px 20px;\r\n    font-size: 21px;\r\n    line-height: 40px;\r\n}\r\n\r\n.btn-lg {\r\n    padding: 12px 18px;\r\n    font-size: 18px;\r\n    line-height: 32px;\r\n}\r\n\r\n.btn-md {\r\n    padding: 10px 16px;\r\n    font-size: 15px;\r\n    line-height: 23px;\r\n}\r\n\r\n.btn-sm {\r\n    padding: 8px 14px;\r\n    line-height: 16px;\r\n    font-size: 11px;\r\n}\r\n\r\n.btn-mini {\r\n    padding: 5px 10px;\r\n    line-height: 14px;\r\n    font-size: 10px;\r\n}\r\n/**====== Button-Size css end ======**/\r\n/**====== Badges-button css start ======**/\r\n\r\n.btn-primary .badge {\r\n    @include labels-badge-inverse(theme-color(primary, base));\r\n}\r\n\r\n.btn-success .badge {\r\n    @include labels-badge-inverse(theme-color(success, base));\r\n}\r\n\r\n.btn-info .badge {\r\n    @include labels-badge-inverse(theme-color(info, base));\r\n}\r\n\r\n.btn-warning .badge {\r\n    @include labels-badge-inverse(theme-color(warning, base));\r\n}\r\n\r\n.btn-danger .badge {\r\n    @include labels-badge-inverse(theme-color(danger, base));\r\n}\r\n/**====== Badges-button css end ======**/\r\n/**====== Social-Buttons css start ======**/\r\n.btn-dribbble,\r\n.btn-dropbox,\r\n.btn-facebook,\r\n.btn-flickr,\r\n.btn-github,\r\n.btn-google-plus,\r\n.btn-instagram,\r\n.btn-linkedin,\r\n.btn-pinterest,\r\n.btn-skype,\r\n.btn-tumblr,\r\n.btn-twitter,\r\n.btn-youtube {\r\n    color: #fff;\r\n\r\n    &:focus,\r\n    &:hover {\r\n        color: #fff;\r\n        box-shadow: none;\r\n    }\r\n}\r\n\r\n.btn-facebook {\r\n    background-color: #3b5998;\r\n}\r\n\r\n.btn-twitter {\r\n    background-color: #00ACED;\r\n}\r\n\r\n.btn-linkedin {\r\n    background-color: #007BB6;\r\n}\r\n\r\n.btn-dribbble {\r\n    background-color: #EA4C89;\r\n}\r\n\r\n.btn-google-plus {\r\n    background-color: #DD4B39;\r\n}\r\n\r\n.btn-instagram {\r\n    background-color: #517FA4;\r\n}\r\n\r\n.btn-pinterest {\r\n    background-color: #CB2027;\r\n}\r\n\r\n.btn-dropbox {\r\n    background-color: #32506D;\r\n}\r\n\r\n.btn-tumblr {\r\n    background-color: #00ACED;\r\n}\r\n/**====== Social-Buttons css end ======**/\r\n/**====== Drop-down-Buttons css start ======**/\r\n\r\n.dropdown-primary,\r\n.dropdown-split-primary {\r\n    .dropdown-menu a {\r\n        &:hover {\r\n            @include dropdown-hover(theme-color(primary, base));\r\n        }\r\n    }\r\n}\r\n\r\n.dropdown-split-success,\r\n.dropdown-success {\r\n    .dropdown-menu a {\r\n        &:hover {\r\n            @include dropdown-hover(theme-color(success, base));\r\n        }\r\n    }\r\n}\r\n\r\n.dropdown-info,\r\n.dropdown-split-info {\r\n    .dropdown-menu a {\r\n        &:hover {\r\n            @include dropdown-hover(theme-color(info, base));\r\n        }\r\n    }\r\n}\r\n\r\n.dropdown-split-warning,\r\n.dropdown-warning {\r\n    .dropdown-menu a {\r\n        &:hover {\r\n            @include dropdown-hover(theme-color(warning, base));\r\n        }\r\n    }\r\n}\r\n\r\n.dropdown-danger,\r\n.dropdown-split-danger {\r\n    .dropdown-menu a {\r\n        &:hover {\r\n            @include dropdown-hover(theme-color(danger, base));\r\n        }\r\n    }\r\n}\r\n\r\n.dropdown-inverse,\r\n.dropdown-split-inverse {\r\n    .dropdown-item {\r\n        transition: all 0.3s ease-in;\r\n    }\r\n\r\n    .dropdown-item:hover {\r\n        @include dropdown-hover(theme-color(inverse, base));\r\n    }\r\n}\r\n/**====== Drop-down-Buttons css end ======**/\r\n/**====== Theme-Buttons css end ======**/\r\n","// Button mixins start\r\n@mixin buttons($back-color,$border-color,$color:#fff,$cursor:pointer,$transition:all ease-in 0.3s){\r\n\tbackground-color: $back-color;\r\n\tborder-color:$border-color;\r\n\tcolor:$color;\r\n\tcursor:$cursor;\r\n\ttransition: $transition;\r\n}\r\n\r\n@mixin buttons-hover($back-color,$border-color,$color:#fff){\r\n\tbackground-color: $back-color;\r\n\tborder-color:$border-color;\r\n}\r\n\r\n@mixin animations-frame($name){\r\n\t@-webkit-keyframes #{$name}{\r\n\t\t@content;\r\n\t}\r\n\t@keyframes #{$name}{\r\n\t\t@content;\r\n\t}\r\n}\r\n\r\n@mixin buttons-active($back-color,$border-color,$color:#fff,$box-shadow:none){\r\n\tbackground-color: $back-color !important;\r\n\tborder-color:$border-color;\r\n\tbox-shadow:$box-shadow;\r\n\tcolor:$color;\r\n}\r\n\r\n@mixin labels-active($back-color,$border-color,$color:#fff,$box-shadow:none){\r\n\tbackground-color: $back-color !important;\r\n\tborder-color:$border-color;\r\n\tbox-shadow:$box-shadow;\r\n\tcolor:$color;\r\n}\r\n\r\n@mixin button-trans($back-color,$border-color){\r\n\tbackground-color:$back-color;\r\n\tborder-color:$border-color;\r\n}\r\n\r\n@mixin buttons-focus($back-color,$color:#fff,$box-shadow:none){\r\n\tbox-shadow:$box-shadow;\r\n\tcolor:$color;\r\n\tbackground-color:$back-color;\r\n}\r\n\r\n@mixin buttons-borders($color,$back-color:#fff){\r\n\tcolor:$color;\r\n\tbackground-color:$back-color;\r\n}\r\n\r\n@mixin dropdown-hover($back-color,$color:#fff){\r\n\tbackground-color:$back-color;\r\n\tcolor:$color;\r\n}\r\n// Button mixins end\r\n\r\n// Label mixin start\r\n@mixin labels-inverse($border-color,$color){\r\n\tborder-color: $border-color;\r\n\tcolor:$color !important;\r\n}\r\n\r\n@mixin labels-badge($back-color:#34495e,$color:#fff){\r\n\tbackground-color: $back-color;\r\n\tcolor:$color;\r\n}\r\n@mixin labels-badge-inverse($color,$back-color:#fff){\r\n\tcolor:$color;\r\n\tbackground-color: $back-color;\r\n}\r\n// Label mixin end\r\n\r\n\r\n@mixin width($width){\r\n\twidth:$width;\r\n}\r\n\r\n\r\n@mixin shadow($shadow...){\r\n\tbox-shadow:$shadow;\r\n\r\n}\r\n\r\n\r\n@mixin theme-keyframes($name) {\r\n\t@-webkit-keyframes #{$name} {\r\n        @content;\r\n    }\r\n\t@keyframes #{$name} {\r\n        @content;\r\n    }\r\n}\r\n","/**  =====================\r\n      Basic form elements css start\r\n==========================  **/\r\n.col-form-label {\r\n    font-size: 14px;\r\n}\r\n\r\n.select2-container {\r\n    width: 100% !important;\r\n}\r\n\r\n.form-group {\r\n    margin-bottom: 1.25em;\r\n\r\n    .form-control-lg {\r\n        font-size: 1.25em;\r\n    }\r\n\r\n    .form-control-round {\r\n        border-radius: 50px;\r\n    }\r\n\r\n    .form-control-static {\r\n        &:focus {\r\n            outline: none;\r\n        }\r\n    }\r\n}\r\n\r\n.form-control {\r\n    font-size: 14px;\r\n    border-radius: 2px;\r\n    border: 1px solid rgb(204, 204, 204);\r\n\r\n    &:focus {\r\n        border: 1px solid $primary-color;\r\n    }\r\n\r\n    &.form-control-normal {\r\n        font-weight: 400;\r\n    }\r\n\r\n    &.form-control-bold {\r\n        font-weight: 600;\r\n    }\r\n\r\n    &.form-control-capitalize {\r\n        text-transform: capitalize;\r\n    }\r\n\r\n    &.form-control-uppercase {\r\n        text-transform: uppercase;\r\n    }\r\n\r\n    &.form-control-lowercase {\r\n        text-transform: lowercase;\r\n    }\r\n\r\n    &.form-control-variant {\r\n        font-variant: small-caps;\r\n    }\r\n\r\n    &.form-control-left {\r\n        text-align: left;\r\n    }\r\n\r\n    &.form-control-center {\r\n        text-align: center;\r\n    }\r\n\r\n    &.form-control-right {\r\n        text-align: right;\r\n    }\r\n\r\n    &.form-control-rtl {\r\n        direction: rtl;\r\n    }\r\n\r\n    &:disabled {\r\n        cursor: not-allowed;\r\n        opacity: 0.5;\r\n    }\r\n}\r\n/*===== Border Inputs start =====*/\r\n$form-border-state: primary, warning, default, danger, success, inverse, info;\r\n$form-border-color: $primary-color, $warning-color, $default-color, $danger-color, $success-color, $inverse-color, $info-color;\r\n@each $var in $form-border-state {\r\n    $i: index($form-border-state, $var);\r\n\r\n    .form-control-#{$var} {\r\n        border-color: nth($form-border-color, $i);\r\n        color: nth($form-border-color, $i);\r\n\r\n        &:focus {\r\n            border-color: nth($form-border-color, $i);\r\n        }\r\n    }\r\n\r\n    .input-group-#{$var} {\r\n        .input-group-addon {\r\n            background-color: nth($form-border-color, $i);\r\n        }\r\n\r\n        .form-control {\r\n            border-color: nth($form-border-color, $i);\r\n        }\r\n    }\r\n}\r\n/*===== Form Input-Text start =====*/\r\n$form-txt-state: primary, warning, default, danger, success, inverse, info;\r\n$form-txt-color: $primary-color, $warning-color, $default-color, $danger-color, $success-color, $inverse-color, $info-color;\r\n@each $var in $form-txt-state {\r\n    $i: index($form-txt-state, $var);\r\n\r\n    .form-txt-#{$var} {\r\n        color: nth($form-txt-color, $i);\r\n\r\n        &:focus {\r\n            color: nth($form-txt-color, $i);\r\n        }\r\n\r\n        &::-moz-placeholder {\r\n            color: nth($form-txt-color, $i);\r\n            opacity: 1;\r\n        }\r\n\r\n        &:-ms-input-placeholder {\r\n            color: nth($form-txt-color, $i);\r\n        }\r\n\r\n        &::-webkit-input-placeholder {\r\n            color: nth($form-txt-color, $i);\r\n        }\r\n    }\r\n}\r\n/*===== Form Background-inputs start =====*/\r\n$form-bg-state: primary, warning, default, danger, success, inverse, info;\r\n$form-bg-color: $primary-color, $warning-color, $default-color, $danger-color, $success-color, $inverse-color, $info-color;\r\n@each $var in $form-bg-state {\r\n    $i: index($form-bg-state, $var);\r\n\r\n    .form-bg-#{$var} {\r\n        background-color: nth($form-bg-color, $i);\r\n        border-color: nth($form-bg-color, $i);\r\n        color: #fff;\r\n\r\n        &:focus {\r\n            background-color: nth($form-bg-color, $i);\r\n            border-color: nth($form-bg-color, $i);\r\n            color: #fff;\r\n        }\r\n\r\n        &::-moz-placeholder {\r\n            color: #fff;\r\n            opacity: 1;\r\n        }\r\n\r\n        &:-ms-input-placeholder {\r\n            color: #fff;\r\n        }\r\n\r\n        &::-webkit-input-placeholder {\r\n            color: #fff;\r\n        }\r\n    }\r\n}\r\n/*===== Form Validation states start =====*/\r\n$form-valid-state: success, warning, danger;\r\n$form-valid-color: $success-color, $warning-color, $danger-color;\r\n@each $var in $form-valid-state {\r\n    $i: index($form-valid-state, $var);\r\n\r\n    .has-#{$var} {\r\n        .col-form-label {\r\n            color: nth($form-valid-color, $i);\r\n        }\r\n\r\n        .form-control-success {\r\n            border-color: nth($form-valid-color, $i);\r\n        }\r\n    }\r\n}\r\n/*===== Form-icon css starts =====*/\r\n.left-icon-control {\r\n    position: relative;\r\n\r\n    input {\r\n        padding-left: 50px;\r\n    }\r\n\r\n    .form-icon {\r\n        position: absolute;\r\n        top: 50%;\r\n        left: 17px;\r\n        margin-top: -12px;\r\n    }\r\n}\r\n\r\n.right-icon-control {\r\n    position: relative;\r\n\r\n    input {\r\n        padding-right: 50px;\r\n    }\r\n\r\n    .form-icon {\r\n        position: absolute;\r\n        bottom: 8px;\r\n        right: 8px;\r\n\r\n        .btn.btn-icon {\r\n            width: 35px;\r\n            height: 35px;\r\n            padding: 0;\r\n        }\r\n    }\r\n    .btn > i{\r\n        margin-right: 0;\r\n    }\r\n\r\n}\r\n/*===== Form-icon css ends =====*/\r\n/*===== Basic form elements ends =====*/\r\n/**  =====================\r\n      Form group-add-on css start\r\n==========================  **/\r\n.input-group {\r\n    margin-bottom: 1.25em;\r\n}\r\n\r\n.input-group-addon {\r\n    background-color: $primary-color;\r\n    color: $white-txt;\r\n}\r\n// .input-group-button [class*=btn-] {\r\n//     padding: 0;\r\n//     margin-bottom: 0;\r\n// }\r\n\r\n.input-group-button .input-group-addon {\r\n    transition: all ease-in 0.3s;\r\n\r\n    &:hover {\r\n        @include buttons-hover(theme-color(primary, hover), theme-color(primary, hover));\r\n\r\n        .btn {\r\n            @include buttons-hover(theme-color(primary, hover), theme-color(primary, hover));\r\n        }\r\n    }\r\n}\r\n\r\n.input-group-dropdown [class*=btn-] {\r\n    margin-bottom: 0;\r\n}\r\n/*===== Form group-add-on ends =====*/\r\n/**  =====================\r\n      Switches css start\r\n==========================  **/\r\n.js-dynamic-disable,\r\n.js-dynamic-enable {\r\n    vertical-align: inherit;\r\n}\r\n/*===== Switches css ends =====*/\r\n/**  =====================\r\n      Radio-button css start\r\n==========================  **/\r\n.form-radio {\r\n    position: relative;\r\n\r\n    .form-help {\r\n        position: absolute;\r\n        width: 100%;\r\n    }\r\n\r\n    label {\r\n        position: relative;\r\n        padding-left: 1.5rem;\r\n        text-align: left;\r\n        color: #333;\r\n        display: block;\r\n        line-height: 1.3;\r\n    }\r\n\r\n    input {\r\n        width: auto;\r\n        opacity: 0.00000001;\r\n        position: absolute;\r\n        left: 0;\r\n    }\r\n}\r\n\r\n.radio {\r\n    .helper {\r\n        position: absolute;\r\n        top: -0.15rem;\r\n        left: -0.25rem;\r\n        cursor: pointer;\r\n        display: block;\r\n        font-size: 1rem;\r\n        -webkit-user-select: none;\r\n        -moz-user-select: none;\r\n        -ms-user-select: none;\r\n        user-select: none;\r\n        color: #999;\r\n\r\n        &::after {\r\n            transform: scale(0);\r\n            background-color: $primary-color;\r\n            border-color: black;\r\n        }\r\n\r\n        &::after,\r\n        &::before {\r\n            content: '';\r\n            position: absolute;\r\n            left: 0;\r\n            top: 3px;\r\n            margin: 0.25rem;\r\n            width: 1rem;\r\n            height: 1rem;\r\n            -webkit-transition: -webkit-transform 0.28s ease;\r\n            transition: -webkit-transform 0.28s ease;\r\n            transition: transform 0.28s ease, -webkit-transform 0.28s ease;\r\n            border-radius: 50%;\r\n            border: 0.125rem solid black;\r\n        }\r\n    }\r\n\r\n    label:hover .helper {\r\n        color: $primary-color;\r\n    }\r\n\r\n    input {\r\n        &:checked ~ .helper::after {\r\n            transform: scale(0.5);\r\n        }\r\n\r\n        &:checked ~ .helper::before {\r\n            color: $primary-color;\r\n            animation: ripple 0.3s linear forwards;\r\n        }\r\n    }\r\n\r\n    &.radiofill {\r\n        input:checked ~ .helper::after {\r\n            transform: scale(1);\r\n        }\r\n\r\n        .helper::after {\r\n            background-color: $primary-color;\r\n        }\r\n    }\r\n\r\n    &.radio-outline {\r\n        input:checked ~ .helper::after {\r\n            transform: scale(0.6);\r\n        }\r\n\r\n        .helper::after {\r\n            background-color: #fff;\r\n            border: 0.225rem solid $primary-color;\r\n        }\r\n    }\r\n\r\n    &.radio-matrial {\r\n        input ~ .helper::after {\r\n            background-color: #fff;\r\n        }\r\n\r\n        input:checked ~ .helper::after {\r\n            transform: scale(0.5);\r\n            box-shadow: 0 1px 7px -1px rgba(0, 0, 0, 0.72);\r\n        }\r\n\r\n        input:checked ~ .helper::before {\r\n            background-color: $primary-color;\r\n        }\r\n    }\r\n\r\n    &.radio-disable {\r\n        label {\r\n            cursor: not-allowed;\r\n        }\r\n        opacity: 0.7;\r\n    }\r\n}\r\n\r\n.radio-inline {\r\n    display: inline-block;\r\n    margin-right: 20px;\r\n}\r\n$radio-bg-state: primary, warning, default, danger, success, inverse, info;\r\n$radio-bg-color: $primary-color, $warning-color, $default-color, $danger-color, $success-color, $inverse-color, $info-color;\r\n@each $var in $radio-bg-state {\r\n    $i: index($radio-bg-state, $var);\r\n\r\n    .radio {\r\n        &.radiofill {\r\n            &.radio-#{$var} {\r\n                .helper::after {\r\n                    background-color: nth($radio-bg-color, $i);\r\n                    border-color: nth($radio-bg-color, $i);\r\n                }\r\n\r\n                .helper::before {\r\n                    border-color: nth($radio-bg-color, $i);\r\n                }\r\n            }\r\n        }\r\n\r\n        &.radio-outline {\r\n            &.radio-#{$var} {\r\n                .helper::after {\r\n                    background-color: #fff;\r\n                    border: 0.225rem solid nth($radio-bg-color, $i);\r\n                }\r\n\r\n                .helper::before {\r\n                    border-color: nth($radio-bg-color, $i);\r\n                }\r\n            }\r\n        }\r\n\r\n        &.radio-matrial {\r\n            &.radio-#{$var} {\r\n                input ~ .helper::after {\r\n                    background-color: #fff;\r\n                    border-color: #fff;\r\n                }\r\n\r\n                input ~ .helper::before {\r\n                    background-color: nth($radio-bg-color, $i);\r\n                    border-color: nth($radio-bg-color, $i);\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n@keyframes ripple {\r\n    0% {\r\n        box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 0.0);\r\n    }\r\n    50% {\r\n        box-shadow: 0px 0px 0px 10px rgba(0, 0, 0, 0.05);\r\n    }\r\n    100% {\r\n        box-shadow: 0px 0px 0px 10px rgba(0, 0, 0, 0);\r\n    }\r\n}\r\n/*===== Radio Button css ends =====*/\r\n/*===== Border-checkbox css start =====*/\r\n.border-checkbox-section {\r\n    .border-checkbox-group {\r\n        display: inline-block;\r\n        .border-checkbox:checked + .border-checkbox-label:before {\r\n            animation: ripple 0.3s linear forwards;\r\n        }\r\n        .border-checkbox:checked + .border-checkbox-label:after {\r\n            -webkit-animation: check linear 0.5s;\r\n            animation: check linear 0.5s;\r\n            opacity: 1;\r\n            border-color:black;\r\n\r\n            .border-checkbox-label {\r\n                &:before {\r\n                    border-color: #eee;\r\n                }\r\n            }\r\n        }\r\n        .border-checkbox-label {\r\n            position: relative;\r\n            display: inline-block;\r\n            cursor: pointer;\r\n            height: 20px;\r\n            line-height: 20px;\r\n            padding-left: 30px;\r\n            margin-right: 15px;\r\n\r\n            &:after {\r\n                content: \"\";\r\n                display: block;\r\n                width: 6px;\r\n                height: 12px;\r\n                opacity: 0.9;\r\n                border-right: 2px solid #eee;\r\n                border-top: 2px solid #eee;\r\n                position: absolute;\r\n                left: 4px;\r\n                top: 11px;\r\n                -webkit-transform: scaleX(-1) rotate(135deg);\r\n                transform: scaleX(-1) rotate(135deg);\r\n                -webkit-transform-origin: left top;\r\n                transform-origin: left top;\r\n            }\r\n\r\n            &:before {\r\n                content: \"\";\r\n                display: block;\r\n                border: 2px solid $primary-color;\r\n                width: 20px;\r\n                height: 20px;\r\n                position: absolute;\r\n                left: 0;\r\n            }\r\n        }\r\n    }\r\n\r\n    .border-checkbox {\r\n        display: none;\r\n\r\n        &:disabled {\r\n            ~ .border-checkbox-label {\r\n                cursor: no-drop;\r\n                color: #ccc;\r\n            }\r\n        }\r\n    }\r\n}\r\n@include theme-keyframes(check) {\r\n    0% {\r\n        height: 0;\r\n        width: 0;\r\n    }\r\n\r\n    25% {\r\n        height: 0;\r\n        width: 6px;\r\n    }\r\n\r\n    50% {\r\n        height: 12px;\r\n        width: 6px;\r\n    }\r\n}\r\n$border-checkbox-bg-state: primary, warning, default, danger, success, inverse, info;\r\n$border-checkbox-bg-color: $primary-color, $warning-color, $default-color, $danger-color, $success-color, $inverse-color, $info-color;\r\n@each $var in $border-checkbox-bg-state {\r\n    $i: index($border-checkbox-bg-state, $var);\r\n\r\n    .border-checkbox-section .border-checkbox-group-#{$var} {\r\n        .border-checkbox-label:before {\r\n            // border: 2px solid nth($border-checkbox-bg-color, $i);\r\n            border: 2px solid black;\r\n        }\r\n\r\n        .border-checkbox:checked + .border-checkbox-label:after {\r\n            // border-color: nth($border-checkbox-bg-color, $i);\r\n            border-color: black;\r\n        }\r\n    }\r\n}\r\n/*===== zoom Fade-in-checkbox css start =====*/\r\n.checkbox-fade,\r\n.checkbox-zoom {\r\n    display: inline-block;\r\n    margin-right: 15px;\r\n\r\n    label {\r\n        line-height: 20px;\r\n\r\n        input[type=\"checkbox\"],\r\n        input[type=\"radio\"] {\r\n            display: none;\r\n\r\n            &:checked + .cr > .cr-icon {\r\n                transform: scale(1) rotateZ(0deg);\r\n                opacity: 1;\r\n            }\r\n\r\n            + .cr > .cr-icon {\r\n                transform: scale(3) rotateZ(-20deg);\r\n                opacity: 0;\r\n                transition: all 0.3s ease-in;\r\n            }\r\n        }\r\n\r\n        &:after {\r\n            content: '';\r\n            display: table;\r\n            clear: both;\r\n        }\r\n    }\r\n\r\n    &.fade-in-disable {\r\n        .cr,\r\n        label {\r\n            color: #ccc;\r\n            cursor: no-drop;\r\n        }\r\n    }\r\n\r\n    .cr {\r\n        border-radius: 0;\r\n        border: 2px solid $primary-color;\r\n        cursor: pointer;\r\n        display: inline-block;\r\n        float: left;\r\n        height: 20px;\r\n        margin-right: 1rem;\r\n        position: relative;\r\n        width: 20px;\r\n\r\n        .cr-icon {\r\n            color: $primary-color;\r\n            font-size: 0.8em;\r\n            left: 0;\r\n            line-height: 0;\r\n            position: absolute;\r\n            right: 0;\r\n            text-align: center;\r\n            top: 50%;\r\n        }\r\n    }\r\n    input[type=\"checkbox\"]:checked{\r\n         + .cr{\r\n             animation: ripple 0.3s linear forwards;\r\n         }\r\n    }\r\n}\r\n\r\n.checkbox-fade {\r\n    label {\r\n        line-height: 20px;\r\n\r\n        input[type=\"checkbox\"],\r\n        input[type=\"radio\"] {\r\n            display: none;\r\n\r\n            &:checked + .cr > .cr-icon {\r\n                transform: scale(1) rotateZ(0deg);\r\n                opacity: 1;\r\n            }\r\n\r\n            + .cr > .cr-icon {\r\n                transform: scale(3) rotateZ(-20deg);\r\n                opacity: 0;\r\n                transition: all 0.3s ease-in;\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n.checkbox-zoom {\r\n    label {\r\n        line-height: 20px;\r\n\r\n        input[type=\"checkbox\"],\r\n        input[type=\"radio\"] {\r\n            display: none;\r\n\r\n            &:checked + .cr > .cr-icon {\r\n                transform: scale3d(1, 1, 1) translate3d(0, 0, 0);\r\n                opacity: 1;\r\n            }\r\n\r\n            + .cr > .cr-icon {\r\n                transform: scale3d(0.2, 0.2, .1) translate3d(0, 0, 0);\r\n                opacity: 0;\r\n                transition: all 0.3s ease-in;\r\n            }\r\n        }\r\n    }\r\n}\r\n$fade-in-checkbox-bg-state: primary, warning, default, danger, success, inverse, info;\r\n$fade-in-checkbox-bg-color: $primary-color, $warning-color, $default-color, $danger-color, $success-color, $inverse-color, $info-color;\r\n@each $var in $fade-in-checkbox-bg-state {\r\n    $i: index($fade-in-checkbox-bg-state, $var);\r\n\r\n    .checkbox-fade,\r\n    .checkbox-zoom {\r\n        &.fade-in-#{$var},\r\n        &.zoom-#{$var} {\r\n            .cr {\r\n                border: 2px solid nth($fade-in-checkbox-bg-color, $i);\r\n\r\n                .cr-icon {\r\n                    color: nth($fade-in-checkbox-bg-color, $i);\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n/*===== Color-checkbox css start =====*/\r\n.checkbox-color {\r\n    display: inline-block;\r\n    margin-right: 20px;\r\n    cursor: pointer;\r\n\r\n    label {\r\n        display: inline-block;\r\n        position: relative;\r\n        padding-left: 10px;\r\n        line-height: 20px;\r\n\r\n        &::before {\r\n            content: \"\";\r\n            display: inline-block;\r\n            position: absolute;\r\n            top: 0;\r\n            width: 20px;\r\n            height: 20px;\r\n            left: 0;\r\n            right: 0;\r\n            text-align: center;\r\n            margin-left: -20px;\r\n            border: 1px solid #ccc;\r\n            border-radius: 0;\r\n            backgro