e;\r\n        display: block;\r\n        width: 100%;\r\n\r\n        &:after,\r\n        &:before {\r\n            content: '';\r\n            height: 2px;\r\n            width: 0;\r\n            bottom: 0;\r\n            position: absolute;\r\n            transition: 0.3s ease all;\r\n            -moz-transition: 0.3s ease all;\r\n            -webkit-transition: 0.3s ease all;\r\n        }\r\n\r\n        &:before {\r\n            left: 50%;\r\n        }\r\n\r\n        &:after {\r\n            right: 50%;\r\n        }\r\n    }\r\n\r\n    .float-label {\r\n        pointer-events: none;\r\n        position: absolute;\r\n        top: 12px;\r\n        left:0;\r\n        font-size: 14px;\r\n        font-weight: 400;\r\n        transition: 0.2s ease all;\r\n        -moz-transition: 0.2s ease all;\r\n        -webkit-transition: 0.2s ease all;\r\n    }\r\n    .material-group{\r\n        display: flex;\r\n        margin-bottom:1.25em;\r\n        .material-addone{\r\n            padding: .5rem .75rem;\r\n            display: flex;\r\n            align-items: center;\r\n            i,span{\r\n                font-size: 20px;\r\n            }\r\n        }\r\n        .form-group{\r\n            flex: 1 1 auto;\r\n            width: 1%;\r\n            margin-bottom: 0;\r\n        }\r\n        &.material-group-lg{\r\n            .material-addone {\r\n                padding: .5rem 1rem;\r\n                i,span{\r\n                    font-size: 1.55rem;\r\n                    font-weight: 500;\r\n                }\r\n            }\r\n            .form-control{\r\n                padding: .5rem 0;\r\n                font-size: 1.25rem;\r\n                line-height: 1.5;\r\n                &:focus,\r\n                &:valid {\r\n                    ~.float-label {\r\n                        top: -15px;\r\n                        font-size: 14px;\r\n                    }\r\n                }\r\n            }\r\n            .float-label{\r\n                font-size: 8.50rem;\r\n            }\r\n        }\r\n        &.material-group-sm{\r\n            .material-addone {\r\n                padding: .25rem .5rem;\r\n                i,span{\r\n\r\n                    font-size: .775rem;\r\n                }\r\n            }\r\n            .form-control{\r\n                padding: .25rem  0;\r\n                font-size: .875rem;\r\n                line-height: 1.5;\r\n                &:focus,\r\n                &:valid {\r\n                    ~.float-label {\r\n                        top: -9px;\r\n                        font-size: 11px;\r\n                    }\r\n                }\r\n            }\r\n            .float-label{\r\n                font-size: 5.775rem;\r\n            }\r\n        }\r\n    }\r\n    $matrial-state: primary, warning, default, danger, success, inverse, info;\r\n    $matrial-color: $primary-color, $warning-color, $default-color, $danger-color, $success-color, $inverse-color, $info-color;\r\n    @each $var in $matrial-state {\r\n        $i: index($matrial-state, $var);\r\n\r\n        .form-#{$var} {\r\n            .form-control {\r\n                &:focus {\r\n                    ~label {\r\n                        // color: nth($matrial-color, $i);\r\n                        color: black;\r\n                    }\r\n\r\n                    ~.form-bar {\r\n                        &:after,\r\n                        &:before {\r\n                            // background: nth($matrial-color, $i);\r\n                            background: black;\r\n                        }\r\n                    }\r\n                }\r\n            }\r\n            &.form-static-label{\r\n                .float-label{\r\n                    // color: nth($matrial-color, $i);\r\n                    color: black;\r\n                }\r\n                .form-control{\r\n                    // border-bottom-color: nth($matrial-color, $i);\r\n                    border-bottom-color: black;\r\n                }\r\n            }\r\n        }\r\n        .material-group-#{$var}{\r\n            .material-addone {\r\n                i,span{\r\n                    color: nth($matrial-color, $i);\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\nselect.form-control{\r\n    &,&:focus,&:hover{\r\n        border-top: none;\r\n        border-right:none;\r\n        border-left:none;\r\n    }\r\n}\r\n/* form controll end*/\r\n\r\n.md-input-file input[type=file] {\r\n    position: absolute;\r\n    top: 0;\r\n    right: 0;\r\n    left: 0;\r\n    bottom: 0;\r\n    width: 100%;\r\n    margin: 0;\r\n    padding: 0;\r\n    font-size: 20px;\r\n    cursor: pointer;\r\n    opacity: 0;\r\n    filter: alpha(opacity=0);\r\n}\r\n.md-input-file{\r\n    overflow: hidden;\r\n    padding-left: 10px;\r\n}\r\n.md-add-on-file{\r\n    float: left;\r\n    height: 3rem;\r\n    line-height: 3rem;\r\n    .btn{\r\n        padding: 7px 20px;\r\n    }\r\n}\r\n.md-form-file ~ .md-form-control{\r\n    opacity: 0;\r\n    position: absolute;\r\n    top: 0;\r\n}\r\n.md-input-wrapper .md-check{\r\n    padding-left: 0;\r\n    top: 0;\r\n}","/**  =====================\r\n      Label-Badges css start\r\n==========================  **/\r\n\r\n.label {\r\n    border-radius: 4px;\r\n    font-size: 75%;\r\n    padding: 4px 7px;\r\n    margin-right: 5px;\r\n    font-weight: 400;\r\n    color: #fff !important;\r\n}\r\n\r\n.label-main {\r\n    display: inline-block;\r\n    vertical-align: middle;\r\n    margin: 8px 5px;\r\n}\r\n\r\n.badge {\r\n    border-radius: 10px;\r\n    padding: 3px 7px;\r\n}\r\n.badge-top-left {\r\n    margin-right: -10px;\r\n    right: 100%;\r\n    top: -3px;\r\n}\r\n.badge-top-right {\r\n    margin-left: -10px;\r\n    top: -3px;\r\n}\r\n.badge-bottom-left {\r\n    margin-left: -33px;\r\n    bottom: -12px;\r\n    right: 97%;\r\n}\r\n.badge-bottom-right {\r\n    margin-left: -10px;\r\n    bottom: -12px;\r\n}\r\n.label.label-lg {\r\n    padding: 8px 21px;\r\n}\r\n\r\n.label.label-md {\r\n    padding: 6px 14px;\r\n}\r\n\r\n.badge-lg {\r\n    padding: 5px 9px;\r\n    font-size: 14px;\r\n}\r\n\r\n.badge-md {\r\n    padding: 4px 8px;\r\n    font-size: 14px;\r\n}\r\n\r\nspan.badge {\r\n    display: inline-block !important;\r\n}\r\n\r\n.label-default {\r\n    @include labels-active(theme-color(default, base), theme-color(default, base));\r\n}\r\n\r\n.label-inverse-default {\r\n    border: 1px solid;\r\n    @include labels-inverse(theme-color(default, base), theme-color(default, base));\r\n}\r\n\r\n.label-inverse-primary {\r\n    border: 1px solid;\r\n    @include labels-inverse(theme-color(primary, base), theme-color(primary, base));\r\n}\r\n\r\n.label-inverse-success {\r\n    border: 1px solid;\r\n    @include labels-inverse(theme-color(success, base), theme-color(success, base));\r\n}\r\n\r\n.label-inverse-warning {\r\n    border: 1px solid;\r\n    @include labels-inverse(theme-color(warning, base), theme-color(warning, base));\r\n}\r\n\r\n.label-inverse-danger {\r\n    border: 1px solid;\r\n    @include labels-inverse(theme-color(danger, base), theme-color(danger, base));\r\n}\r\n\r\n.label-inverse-info {\r\n    border: 1px solid;\r\n    @include labels-inverse(theme-color(info, base), theme-color(info, base));\r\n}\r\n\r\n.label-inverse-info-border {\r\n    border: 1px solid;\r\n    @include labels-inverse(theme-color(inverse, base), theme-color(inverse, base));\r\n}\r\n\r\n.badge-inverse-default {\r\n    border: 1px solid;\r\n    @include labels-inverse(theme-color(default, base), theme-color(default, base));\r\n}\r\n\r\n.badge-inverse-primary {\r\n    border: 1px solid;\r\n    @include labels-inverse(theme-color(primary, base), theme-color(primary, base));\r\n}\r\n\r\n.badge-inverse-success {\r\n    border: 1px solid;\r\n    @include labels-inverse(theme-color(success, base), theme-color(success, base));\r\n}\r\n\r\n.badge-inverse-warning {\r\n    border: 1px solid;\r\n    @include labels-inverse(theme-color(warning, base), theme-color(warning, base));\r\n}\r\n\r\n.badge-inverse-danger {\r\n    border: 1px solid;\r\n    @include labels-inverse(theme-color(danger, base), theme-color(danger, base));\r\n}\r\n\r\n.badge-inverse-info {\r\n    border: 1px solid;\r\n    @include labels-inverse(theme-color(inverse, base), theme-color(inverse, base));\r\n}\r\n\r\n.label-icon{\r\n    position: relative;\r\n    label {\r\n       position: absolute;\r\n   }\r\n}\r\n\r\n.icofont.icofont-envelope {\r\n    font-size: 20px;\r\n}\r\n\r\n\r\n\r\n/*===== Label and Badges css end =====*/\r\n","/* ======================\r\n    All Icon Common Page css\r\n======================== */\r\n\r\n.data-table-main.icon-list-demo [class*=col-] {\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.data-table-main.icon-svg-demo [class*=col-] {\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.icon-list-demo i {\r\n    border: 1px solid #eceeef;\r\n    border-radius: 3px;\r\n    color: rgba(43, 61, 81, 0.7);\r\n    display: inline-block;\r\n    font-size: 24px;\r\n    height: 50px;\r\n    line-height: 50px;\r\n    margin: 0 12px 0 0;\r\n    text-align: center;\r\n    vertical-align: middle;\r\n    width: 50px;\r\n}\r\n\r\n.icon-list-demo div {\r\n    cursor: pointer;\r\n    white-space: nowrap;\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.icon-list-demo i:hover {\r\n    color: #64b0f2;\r\n}\r\n\r\n\r\n/**====== Flag-icon css start ======**/\r\n\r\n.flags .f-item {\r\n    padding: 12px;\r\n    border: 1px solid #ddd;\r\n    margin-right: 15px;\r\n    display: inline-block;\r\n}\r\n\r\n.data-table-main.flags [class*=col-] {\r\n    display: flex;\r\n    align-items: center;\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.content-flag label {\r\n    margin-bottom: 0;\r\n    cursor: pointer;\r\n}\r\n\r\n.content-flag {\r\n    .txt-ellipsis {\r\n        text-overflow: ellipsis;\r\n        white-space: nowrap;\r\n        width: 120px;\r\n        overflow: hidden;\r\n    }\r\n}\r\n\r\n.outer-ellipsis {\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n    vertical-align: middle;\r\n    white-spa