.j-ratings input:checked + label,\r\n.j-pro .j-ratings input:checked + label ~ label {\r\n    color: $primary-color\r\n}\r\n\r\n.j-unit .checkbox-fade {\r\n    display: block;\r\n    margin-top: 20px\r\n}\r\n\r\n// j-pro css end\r\n// j-forms css start\r\n.j-forms {\r\n    button i {\r\n        margin-right: 0\r\n    }\r\n    .checkbox-fade {\r\n        margin-top: 10px;\r\n        .disabled-view {\r\n            opacity: 0.5;\r\n            cursor: not-allowed;\r\n        }\r\n    }\r\n    .link {\r\n        color: $primary-color;\r\n        border-bottom: none;\r\n        text-transform: capitalize;\r\n        margin-bottom: 15px\r\n    }\r\n    .label {\r\n        margin-bottom: 15px\r\n    }\r\n    .btn-primary:disabled {\r\n        background-color: $default-color;\r\n        border-color: $default-color;\r\n    }\r\n    .radio-toggle,\r\n    .checkbox-toggle,\r\n    .inline-group .radio-toggle,\r\n    .inline-group .checkbox-toggle {\r\n        padding: 9px 0 8px 0px;\r\n    }\r\n    box-shadow: none;\r\n    border: 1px solid rgba(0, 0, 0, .15);\r\n    .header {\r\n        background-color: $primary-color;\r\n        border-top: 1px solid $primary-color;\r\n        box-shadow: none\r\n    }\r\n    .divider-text span {\r\n        color: #222\r\n    }\r\n    .widget .addon,\r\n    .widget .addon-btn {\r\n        background-color: $primary-color;\r\n        color: $white-txt;\r\n        &:hover,\r\n        &:focus {\r\n            background-color: lighten($primary-color, 5%);\r\n            i {\r\n                color: $white-txt\r\n            }\r\n        }\r\n        i {\r\n            color: $white-txt\r\n        }\r\n    }\r\n    .footer {\r\n        background-color: #fff;\r\n        border-top: 1px dashed #1abc9c;\r\n        padding: 20px 25px;\r\n        position: inherit;\r\n        button {\r\n            float: right;\r\n            margin-bottom: 0\r\n        }\r\n    }\r\n    .stepper {\r\n        .stepper-arrow {\r\n            background-color: $