-3-prod{\r\n\theight: 100px !important;\r\n    width: 100px !important;\r\n\tmargin: 0 auto;\r\n}\r\n.quater-card{\r\n    .progress{\r\n        height: 4px;\r\n    }\r\n}\r\n#sale-chart1,\r\n#sale-chart2,\r\n#sale-chart3{\r\n    height: 150px !important;\r\n    width: 100%;\r\n}\r\n","/**  =====================\r\n      Alert css start\r\n==========================  **/\r\n.alert {\r\n    font-size: 15px;\r\n    margin-bottom: 2rem;\r\n}\r\n\r\n.alert.icons-alert {\r\n    border-left-width: 48px;\r\n    .alert-icon {\r\n        left: 15px;\r\n        position: relative;\r\n        text-align: center;\r\n        top: 0;\r\n        z-index: 3;\r\n    }\r\n    p {\r\n        line-height: 21px;\r\n        margin-bottom: 0;\r\n    }\r\n}\r\n\r\n.close {\r\n    font-size: 16px;\r\n    margin-top: 5px;\r\n}\r\n\r\n\r\n/*===== Primary alert ===== */\r\n\r\n$alert-name:default,\r\nprimary,\r\nsuccess,\r\ninfo,\r\nwarning,\r\ndanger;\r\n$alert-color:$default-color,\r\n$primary-color,\r\n$success-color,\r\n$info-color,\r\n$warning-color,\r\n$danger-color;\r\n@each $var in $alert-name {\r\n    $i: index($alert-name, $var);\r\n    .alert-#{$var} {\r\n        background-color: #fff;\r\n        border-color: nth($alert-color, $i);\r\n        color: nth($alert-color, $i);\r\n    }\r\n}\r\n\r\n\r\n/*===== Border alert ===== */\r\n\r\n$alert-name:default,\r\nprimary,\r\nsuccess,\r\ninfo,\r\nwarning,\r\ndanger;\r\n$alert-color:$default-color,\r\n$primary-color,\r\n$success-color,\r\n$info-color,\r\n$warning-color,\r\n$danger-color;\r\n@each $var in $alert-name {\r\n    $i: index($alert-name, $var);\r\n    .border-#{$var} {\r\n        border-color: transparent;\r\n        border-left: 3px solid nth($alert-color, $i);\r\n        border-radius: 0;\r\n        box-shadow: 0 0 1px #999;\r\n        color: nth($alert-color, $i);\r\n    }\r\n}\r\n\r\n\r\n/*===== Solid-background alert ===== */\r\n\r\n$alert-name:default,\r\nprimary,\r\nsuccess,\r\ninfo,\r\nwarning,\r\ndanger;\r\n$alert-color:$