\n\t.#{$var}\r\n\t{\r\n\t\tbackground-color: nth($progress-color,$i);\r\n\t}\r\n}\r\n/**====== Progress-bar css end ======**/","/**  =====================\r\n      Pre-loader css start\r\n==========================  **/\r\n\r\n\r\n/*===== Loader1 =====*/\r\n\r\n.loader {\r\n    margin: 18px auto;\r\n    top: 65px;\r\n    left: 65px;\r\n    right: 0;\r\n    width: 100px;\r\n    height: 100px;\r\n    position: relative;\r\n    transform: translate(-50%, -50%);\r\n    &.animation-start .circle {\r\n        animation: rotating 2s ease-in-out infinite;\r\n    }\r\n    &.animation-stop .circle {\r\n        animation-iteration-count: 1;\r\n    }\r\n    .circle {\r\n        width: 50px;\r\n        height: 50px;\r\n        display: block;\r\n        position: absolute;\r\n        transform-origin: 50% 50%;\r\n        animation-iteration-count: 0;\r\n    }\r\n    .circle:after {\r\n        content: '';\r\n        top: 0;\r\n        left: 0;\r\n        width: 10px;\r\n        height: 10px;\r\n        display: block;\r\n        border-radius: 50%;\r\n        transform: translate(-50%, -50%);\r\n        background-color: #1abc9c;\r\n    }\r\n    .circle.delay-1 {\r\n        animation-delay: 0.125s;\r\n    }\r\n    .circle.size-1:after {\r\n        width: 11px;\r\n        height: 11px;\r\n    }\r\n    .circle.delay-2 {\r\n        animation-delay: 0.25s;\r\n    }\r\n    .circle.size-2:after {\r\n        width: 12px;\r\n        height: 12px;\r\n    }\r\n    .circle.delay-3 {\r\n        animation-delay: 0.375s;\r\n    }\r\n    .circle.size-3:after {\r\n        width: 13px;\r\n        height: 13px;\r\n    }\r\n    .circle.delay-4 {\r\n        animation-delay: 0.5s;\r\n    }\r\n    .circle.size-4:after {\r\n        width: 14px;\r\n        height: 14px;\r\n    }\r\n    .circle.delay-5 {\r\n        animation-delay: 0.625s;\r\n    }\r\n    .circle.size-5:after {\r\n        width: 15px;\r\n        height: 15px;\r\n    }\r\n    .circle.delay-6 {\r\n        animation-delay: 0.75s;\r\n    }\