\r\n        border-bottom-color: #1b8bf9;\r\n    }\r\n    62.5% {\r\n        border-bottom-color: rgba(27, 139, 249, 0.8);\r\n    }\r\n    75% {\r\n        border-bottom-color: rgba(27, 139, 249, 0.6);\r\n    }\r\n    87.5% {\r\n        border-bottom-color: rgba(27, 139, 249, 0.4);\r\n    }\r\n}\r\n\r\n\r\n/*===== Loader4 =====*/\r\n\r\n.preloader4 {\r\n    width: 30px;\r\n    height: 30px;\r\n    position: relative;\r\n    margin: 55px auto;\r\n}\r\n\r\n.double-bounce1,\r\n.double-bounce2 {\r\n    width: 100%;\r\n    height: 100%;\r\n    border-radius: 50%;\r\n    background-color: $primary-color;\r\n    opacity: 0.6;\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    -webkit-animation: bounce 2.0s infinite ease-in-out;\r\n    animation: bounce 2.0s infinite ease-in-out;\r\n    -webkit-animation-delay: -1.0s;\r\n    animation-delay: -1.0s;\r\n}\r\n\r\n@-webkit-keyframes bounce {\r\n    0%,\r\n    100% {\r\n        -webkit-transform: scale(0.0)\r\n    }\r\n    50% {\r\n        -webkit-transform: scale(1.0)\r\n    }\r\n}\r\n\r\n@keyframes bounce {\r\n    0%,\r\n    100% {\r\n        transform: scale(0.0);\r\n        -webkit-transform: scale(0.0);\r\n    }\r\n    50% {\r\n        transform: scale(1.0);\r\n        -webkit-transform: scale(1.0);\r\n    }\r\n}\r\n\r\n\r\n/*===== Loader5 =====*/\r\n\r\n.preloader5 {\r\n    margin: 55px 0;\r\n    text-align: center;\r\n    .circle-5 {\r\n        display: inline-block;\r\n        padding: 11px;\r\n        margin: 0 0.6em;\r\n        background: $primary-color;\r\n        border-radius: 100%;\r\n    }\r\n    .l {\r\n        -webkit-animation: pulse 2s infinite linear;\r\n        animation: pulse 2s infinite linear;\r\n    }\r\n    .m {\r\n        -webkit-animation: pulse 2s infinite linear;\r\n        animation: pulse 2s infinite linear;\r\n        -webkit-animation-delay: 0.3s;\r\n        animation-delay: 0.3s;\r\n    }\r\n    .r {\r\n        -webkit-animation: pulse 2s infinite linear;\r\n        animation: pulse 2s infinite linear;\r\n        -webkit-animation-delay: 0.6s;\r\n        animation-delay: 0.6s;\r\n    }\r\n}\r\n\r\n@-webkit-keyframes pulse {\r\n    10% {\r\n        -webkit-transform: scale(1);\r\n        transform: scale(1);\r\n    }\r\n    20% {\r\n        -webkit-transform: scale(1);\r\n        transform: scale(1);\r\n    }\r\n    30% {\r\n        -webkit-transform: scale(1.7);\r\n        transform: scale(1.7);\r\n    }\r\n    50% {\r\n        -webkit-transform: scale(1.7);\r\n        transform: scale(1.7);\r\n    }\r\n    70% {\r\n        -webkit-transform: scale(1.7);\r\n        transform: scale(1.7);\r\n    }\r\n    80% {\r\n        -webkit-transform: scale(1);\r\n        transform: scale(1);\r\n    }\r\n    90% {\r\n        -webkit-transform: scale(1);\r\n        transform: scale(1);\r\n    }\r\n    100% {\r\n        -webkit-transform: scale(1);\r\n        transform: scale(1);\r\n    }\r\n}\r\n\r\n@keyframes pulse {\r\n    10% {\r\n        -webkit-transform: scale(1);\r\n        transform: scale(1);\r\n    }\r\n    20% {\r\n        -webkit-transform: scale(1);\r\n        transform: scale(1);\r\n    }\r\n    30% {\r\n        -webkit-transform: scale(1.7);\r\n        transform: scale(1.7);\r\n    }\r\n    50% {\r\n        -webkit-transform: scale(1.7);\r\n        transform: scale(1.7);\r\n    }\r\n    70% {\r\n        -webkit-transform: scale(1.7);\r\n        transform: scale(1.7);\r\n    }\r\n    80% {\r\n        -webkit-transform: scale(1);\r\n        transform: scale(1);\r\n    }\r\n    90% {\r\n        -webkit-transform: scale(1);\r\n        transform: scale(1);\r\n    }\r\n    100% {\r\n        -webkit-transform: scale(1);\r\n        transform: scale(1);\r\n    }\r\n}\r\n\r\n\r\n/*===== Loader6 =====*/\r\n\r\n.preloader6 {\r\n    border-radius: 100%;\r\n    width: 100px;\r\n    height: 100px;\r\n    position: relative;\r\n    overflow: hidden;\r\n    z-index: 1;\r\n    background: inherit;\r\n    margin: 18px 0;\r\n}\r\n\r\n.preloader6 hr {\r\n    border: 0;\r\n    background: inherit;\r\n    width: 80%;\r\n    height: 80%;\r\n    margin: 10%;\r\n    border-radius: 100%;\r\n}\r\n\r\n.preloader6::before,\r\n.preloader6::after,\r\n.preloader6 hr::before,\r\n.preloader6 hr::after {\r\n    content: '';\r\n    position: absolute;\r\n    top: 0;\r\n    z-index: -1;\r\n    left: 33px;\r\n    border: 17px solid transparent;\r\n    border-bottom: 50px solid $primary-color;\r\n    border-top: 50px solid $primary-color;\r\n    animation: rotateColor 1000ms infinite;\r\n}\r\n\r\n.preloader6::after {\r\n    transform: rotate(45deg);\r\n    animation-delay: 125ms;\r\n}\r\n\r\n.preloader6 hr::before {\r\n    transform: rotate(90deg);\r\n    animation-delay: 250ms;\r\n}\r\n\r\n.preloader6 hr::after {\r\n    transform: rotate(135deg);\r\n    animation-delay: 375ms;\r\n}\r\n\r\n.loader-block,\r\n.loader {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n}\r\n\r\n@keyframes rotateColor {\r\n    0% {\r\n        border-top-color: rgba(26, 188, 156, 1)\r\n    }\r\n    12.5% {\r\n        border-bottom-color: rgba(26, 188, 156, 0.8)\r\n    }\r\n    25% {\r\n        border-bottom-color: rgba(26, 188, 156, 0.6)\r\n    }\r\n    37.5% {\r\n        border-bottom-color: rgba(26, 188, 156, 0.4)\r\n    }\r\n    50% {\r\n        border-bottom-color: rgba(26, 188, 156, 0.6)\r\n    }\r\n    50.00001% {\r\n        border-bottom-color: rgba(26, 188, 156, 1);\r\n    }\r\n    62.5% {\r\n        border-bottom-color: rgba(26, 188, 156, 0.8);\r\n    }\r\n    75% {\r\n        border-bottom-color: rgba(26, 188, 156, 0.6);\r\n    }\r\n    87.5% {\r\n        border-bottom-color: rgba(26, 188, 156, 0.4);\r\n    }\r\n}\r\n\r\n\r\n/*========= Pre-loader css end ============*/","/*range slider start */\r\n\r\n.range-slider {\r\n    min-height: 200px;\r\n    display: flex;\r\n    align-items: center;\r\n    margin-left: 15px;\r\n    b{\r\n        margin-top: -10px;\r\n    }\r\n}\r\n\r\n#RGB {\r\n    height: 10px;\r\n    bac