: border-box;\r\n    display: flex;\r\n    flex-direction: column;\r\n    max-height: 100%;\r\n    white-space: normal;\r\n    background-color: #fff;\r\n    border: 1px solid $border-color;\r\n    @if $enable-rounded {\r\n      border-radius: $border-radius;\r\n    } @else {\r\n      border-radius: 0;\r\n    }\r\n  }\r\n  .taskboard-header {\r\n    position: relative;\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    font-family: $font-family-headers;\r\n    text-transform: uppercase;\r\n    letter-spacing: 0.02rem;\r\n    font-size: 13px;\r\n    padding: ($spacer/2) ($spacer/2) 0;\r\n  }\r\n  .taskboard-task {\r\n    position: relative;\r\n    display: flex;\r\n    flex-direction: column;\r\n\r\n    border: $card-border-width solid $card-border-color;\r\n    @include border-radius($card-border-radius);\r\n    padding: ($spacer/2);\r\n    margin-bottom: ($spacer/2);\r\n    &:last-child {\r\n      margin-bottom: 0;\r\n    }\r\n    &:hover {\r\n      cursor: grab;\r\n    }\r\n  }\r\n  .taskboard-task:after {\r\n    content: \"\";\r\n    position: absolute;\r\n    border: 4px solid transparent;\r\n    top: 0;\r\n    border-top-width: 12px;\r\n    border-bottom-color: transparent;\r\n    right: 6px;\r\n  }\r\n  .taskboard-task.task-status-success:after {\r\n    border-top-color: $success-color;\r\n    border-right-color: $success-color;\r\n    border-left-color: $success-color;\r\n  }\r\n  .taskboard-task.task-status-info:after {\r\n    border-top-color: $info-color;\r\n    border-right-color: $info-color;\r\n    border-left-color: $info-color;\r\n  }\r\n  .taskboard-task.task-status-warning:after {\r\n    border-top-color: $warning-color;\r\n    border-right-color: $warning-color;\r\n    border-left-color: $warning-color;\r\n  }\r\n  .taskboard-task.task-status-danger:after {\r\n    border-top-color: $danger-color;\r\n    border-right-color: $danger-color;\r\n    border-left-color: $danger-color;\r\n  }\r\n  .taskboard-cards {\r\n    padding: ($spacer/2);\r\n    box-sizing: border-box;\r\n    overflow-x: hidden;\r\n    overflow-y: auto;\r\n  }\r\n  .taskboard-task-title {\r\n    margin-bottom: ($spacer/2);\r\n  }\r\n}\r\n",".ripple {\r\n  position: relative;\r\n  overflow: hidden;\r\n  &:after {\r\n    content: \"\";\r\n    display: block;\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 100%;\r\n    top: 0;\r\n    left: 0;\r\n    pointer-events: none;\r\n    background-image: radial-gradient(circle, #000 10%, transparent 10.01%);\r\n    background-repeat: no-repeat;\r\n    background-position: 50%;\r\n    transform: scale(10, 10);\r\n    opacity: 0;\r\n    transition: transform .5s, opacity 1s;\r\n  }\r\n  &:active:after {\r\n    transform: scale(0, 0);\r\n    opacity: .2;\r\n    transition: 0s;\r\n  }\r\n\r\n  &.menu-ripple {\r\n    width: 100%;\r\n    height: 100%;\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    z-index: 1030;\r\n  }\r\n\r\n  &.light:after{\r\n    background-image: radial-gradient(circle, #fff 10%, transparent 10.01%);\r\n  }\r\n}\r\n",".ag-slider .noUi-horizontal {\r\n  height: 16px;\r\n  background: transparent;\r\n  border-radius: 0px;\r\n  border: none;\r\n  border-top: 1px solid #D3D3D3;\r\n  box-shadow: none;\r\n  cursor: pointer;\r\n  .noUi-handle {\r\n    width: 20px;\r\n    height: 20px;\r\n    top: -10px;\r\n    border: 1px solid transparent;\r\n    border-radius: 100%;\r\n    background-color: #4680ff;\r\n    box-shadow: none;\r\n    &:after, &:before {\r\n      background: transparent;\r\n    }\r\n  }\r\n  .noUi-connects {\r\n    padding: 10px;\r\n    top: -10px;\r\n    .noUi-connect {\r\n      background: #4680ff;\r\n      height: 3px;\r\n      top: 8px;\r\n    }\r\n  }\r\n\r\n  .noUi-pips-horizontal {\r\n    top: 0;\r\n    padding: 0;\r\n  }\r\n  .noUi-tooltip {\r\n    border: 1px solid $inverse-color;\r\n    border-radius: 5px;\r\n    background: $inverse-color;\r\n    color: #fff;\r\n    padding: 5px 15px;\r\n    transform: translate(-50%,-15px);\r\n    &:after{\r\n      content: \"\";\r\n      position: absolute;\r\n      border:6px solid transparent;\r\n      border-bottom-color: $inverse-color;\r\n      border-right-color: $inverse-color;\r\n      bottom: -10px;\r\n      left: 50%;\r\n      transform: rotate(45deg) translate(-50%,0);\r\n    }\r\n  }\r\n}\r\n","//=== wizard\r\n.ngx-form-wizard {\r\n  .nav-item{\r\n    a.nav-link {\r\n      background: #eee;\r\n      color: #aaa;\r\n      margin: 0 0.5em 0.5em;\r\n      padding: 1em 1em;\r\n      border-radius: 5px;\r\n      &.active{\r\n        background-color: $primary-color;\r\n        color: #fff;\r\n      }\r\n    }\r\n  }\r\n  .nav-tabs{\r\n    border-bottom: none;\r\n  }\r\n  .tab-pane{\r\n    border: 1px solid $theme-border;\r\n    padding: 20px;\r\n    margin: 8px;\r\n    border-radius: 5px;\r\n  }\r\n}\r\n.arc-wizard {\r\n  &.arc-custom {\r\n    .steps-indicator li:after {\r\n      border-radius: 5px !important;\r\n    }\r\n  }\r\n  .centered-content {\r\n    text-align: center;\r\n  }\r\n  .wizard-steps{\r\n    border-radius: 5px;\r\n    border:1px solid $theme-border;\r\n    padding: 20px;\r\n  }\r\n\r\n  .small ul.steps-indicator li.current:after,\r\n  .small ul.steps-indicator li.editing:after {\r\n    background-color: $primary-color !important;\r\n  }\r\n\r\n  .large-filled-symbols ul.steps-indicator li.current:after,\r\n  .large-filled-symbols ul.steps-indicator li.editing:after {\r\n    background-color: $primary-color !important;\r\n    color: #fff !important;\r\n  }\r\n\r\n  .large-filled-symbols ul.steps-indicator li.done:after {\r\n    color: #fff !important;\r\n  }\r\n\r\n  .large-empty-symbols ul.steps-indicator li.current:after,\r\n  .large-empty-symbols ul.steps-indicator li.editing:after {\r\n    border-color: $primary-color !important;\r\n    color: $primary-color !important;\r\n  }\r\n}\r\n",".error-400, .error-403, .error-404, .error-500, .error-503 {\r\n  position: fixed;\r\n  height: 100%;\r\n  width: 100%;