n  }\r\n\r\n  ul.steps-indicator li:after {\r\n    font-weight: 900;\r\n  }\r\n\r\n  .small ul.steps-indicator li.current:after,\r\n  .small ul.steps-indicator li.editing:after {\r\n    background-color: $primary-color !important;\r\n  }\r\n\r\n  .large-filled-symbols ul.steps-indicator li.current:after,\r\n  .large-filled-symbols ul.steps-indicator li.editing:after {\r\n    background-color: $primary-color !important;\r\n    color: #fff !important;\r\n\r\n  }\r\n\r\n  .large-filled-symbols ul.steps-indicator li.done:after {\r\n    color: #fff !important;\r\n  }\r\n\r\n  .large-empty-symbols ul.steps-indicator li.current:after,\r\n  .large-empty-symbols ul.steps-indicator li.editing:after {\r\n    border-color: $primary-color !important;\r\n    color: $primary-color !important;\r\n  }\r\n\r\n  aw-wizard-navigation-bar.horizontal.large-filled-symbols ul.steps-indicator li .step-indicator {\r\n    color: #fff;\r\n    font-size: 24px;\r\n  }\r\n\r\n  aw-wizard-navigation-bar.horizontal.large-empty-symbols ul.steps-indicator li .step-indicator {\r\n    font-size: 24px;\r\n  }\r\n}\r\n","/**  =====================\r\n      Data Tables css start\r\n==========================  **/\r\n.table {\r\n    td,\r\n    th {\r\n        border-top: 1px solid $theme-border;\r\n        white-space: nowrap;\r\n        padding: 1.05rem 0.75rem;\r\n    }\r\n\r\n    thead th {\r\n        border-bottom: 1px solid $theme-border;\r\n        font-size: 13px;\r\n        color: $headings-color;\r\n        background: $theme-bg-color;\r\n        text-transform: uppercase;\r\n        font-family: 'Maven Pro';\r\n        font-weight: bold;\r\n    }\r\n\r\n    tbody + tbody {\r\n        border-top: 2px solid $theme-border;\r\n    }\r\n\r\n    &.table-dark {\r\n        thead th {\r\n            background-color: #11171a;\r\n        }\r\n    }\r\n\r\n    &.dataTable[class*=\"table-\"] {\r\n        thead th {\r\n            background-color: $theme-bg-color;\r\n        }\r\n    }\r\n}\r\n/* Border versions */\r\n\r\n.table-bordered {\r\n    border: 1px solid $theme-border;\r\n\r\n    td,\r\n    th {\r\n        border: 1px solid $theme-border;\r\n    }\r\n    td{\r\n        font-family: 'Maven Pro';\r\n        font-weight: normal;\r\n    }\r\n}\r\n/* Zebra-striping */\r\n\r\n.table-striped {\r\n    tbody tr:nth-of-type(2n+1) {\r\n        background-color: transparentize($primary-color,0.97);\r\n    }\r\n}\r\n/* Hover effect */\r\n\r\n.table-hover {\r\n    tbody tr {\r\n        &:hover {\r\n            background-color: transparentize($primary-color,0.97);\r\n        }\r\n    }\r\n}\r\n/* Table backgrounds */\r\n\r\n.table {\r\n    .thead-dark {\r\n        th {\r\n            color: #fff;\r\n            background-color: $dark-color;\r\n            border-color: darken($dark-color,10%);\r\n        }\r\n    }\r\n}\r\n\r\n.table-dark {\r\n    color: #fff;\r\n    background-color: $dark-color;\r\n\r\n    td,\r\n    th,\r\n    thead th {\r\n        border-color: darken($dark-color,10%);\r\n    }\r\n\r\n    &.table-striped {\r\n        tbody tr:nth-of-type(odd) {\r\n            background-color: darken($dark-color,2%);\r\n        }\r\n    }\r\n\r\n    &.table-hover {\r\n        tbody tr {\r\n            &:hover {\r\n                background-color: darken($dark-color,5%);\r\n            }\r\n        }\r\n    }\r\n\r\n    thead th {\r\n        // background-color: $dark-color;\r\n        color: #fff;\r\n    }\r\n}\r\n/* fixed header position */\r\n\r\ntable.dataTable.fixedHeader-floating {\r\n    top: 0 !important;\r\n    @media screen and (max-width:992px) {\r\n        display: none !important;\r\n    }\r\n}\r\n@media screen and (max-width:992px) {\r\n    .fixedHeader-locked {\r\n        display: none !important;\r\n    }\r\n}\r\n/**  =====================\r\n      Foo-table css start\r\n==========================  **/\r\n\r\n.footable .pagination > {\r\n    .active > {\r\n        a,\r\n        span {\r\n            background-color: $primary-color;\r\n            bord